/2/300 blues\n\n// -------- Green --------\n$green-000:       #f0fff4 !default;\n$green-100:       #dcffe4 !default;\n$green-200:       #bef5cb !default;\n$green-300:       #85e89d !default;\n$green-400:       #34d058 !default;\n$green-500:       #28a745 !default; // Default. passes AA Large\n$green-600:       #22863a !default; // Text green, passes AA on #fff\n$green-700:       #176f2c !default;\n$green-800:       #165c26 !default;\n$green-900:       #144620 !default;\n\n// -------- Yellow --------\n$yellow-000:      #fffdef !default;\n$yellow-100:      #fffbdd !default;\n$yellow-200:      #fff5b1 !default;\n$yellow-300:      #ffea7f !default;\n$yellow-400:      #ffdf5d !default;\n$yellow-500:      #ffd33d !default;\n$yellow-600:      #f9c513 !default;\n$yellow-700:      #dbab09 !default;\n$yellow-800:      #b08800 !default;\n$yellow-900:      #735c0f !default;\n\n// -------- Orange --------\n$orange-000:      #fff8f2 !default;\n$orange-100:      #ffebda !default;\n$orange-200:      #ffd1ac !default;\n$orange-300:      #ffab70 !default;\n$orange-400:      #fb8532 !default;\n$orange-500:      #f66a0a !default; // Default. passes AA Large with #fff\n$orange-600:      #e36209 !default;\n$orange-700:      #d15704 !default;\n$orange-800:      #c24e00 !default;\n$orange-900:      #a04100 !default;\n\n// -------- Red --------\n$red-000:         #ffeef0 !default;\n$red-100:         #ffdce0 !default;\n$red-200:         #fdaeb7 !default;\n$red-300:         #f97583 !default;\n$red-400:         #ea4a5a !default;\n$red-500:         #d73a49 !default; // Default. passes AA\n$red-600:         #cb2431 !default;\n$red-700:         #b31d28 !default;\n$red-800:         #9e1c23 !default;\n$red-900:         #86181d !default;\n\n// -------- Purple --------\n$purple-000:      #f5f0ff !default;\n$purple-100:      #e6dcfd !default;\n$purple-200:      #d1bcf9 !default;\n$purple-300:      #b392f0 !default;\n$purple-400:      #8a63d2 !default;\n$purple-500:      #6f42c1 !default; // passes AA with #fff\n$purple-600:      #5a32a3 !default;\n$purple-700:      #4c2889 !default;\n$purple-800:      #3a1d6e !default;\n$purple-900:      #29134e !default;\n\n// -------- Pink --------\n$pink-000:      #ffeef8 !default;\n$pink-100:      #fedbf0 !default;\n$pink-200:      #f9b3dd !default;\n$pink-300:      #f692ce !default;\n$pink-400:      #ec6cb9 !default;\n$pink-500:      #ea4aaa !default;\n$pink-600:      #d03592 !default;\n$pink-700:      #b93a86 !default;\n$pink-800:      #99306f !default;\n$pink-900:      #6d224f !default;\n\n// -------- Fades --------\n// Black based on same hue as $gray-900\n$black: #1b1f23 !default;\n$white: #fff !default;\n\n$black-fade-15:      rgba($black, 0.15) !default;\n$black-fade-30:      rgba($black, 0.3) !default;\n$black-fade-50:      rgba($black, 0.5) !default;\n$black-fade-70:      rgba($black, 0.7) !default;\n$black-fade-85:      rgba($black, 0.85) !default;\n\n$white-fade-15:      rgba($white, 0.15) !default;\n$white-fade-30:      rgba($white, 0.3) !default;\n$white-fade-50:      rgba($white, 0.5) !default;\n$white-fade-70:      rgba($white, 0.7) !default;\n$white-fade-85:      rgba($white, 0.85) !default;\n\n// -------- Color defaults --------\n$red:         $red-500 !default;\n$purple:      $purple-500 !default;\n$blue:        $blue-500 !default;\n$green:       $green-500 !default;\n$yellow:      $yellow-500 !default;\n$orange:      $orange-500 !default;\n\n$gray-dark:   $gray-900 !default;\n$gray-light:  $gray-400 !default;\n$gray:        $gray-500 !default;\n\n// -------- Color gradient maps --------\n\n$grays: (\n  0: $gray-000,\n  1: $gray-100,\n  2: $gray-200,\n  3: $gray-300,\n  4: $gray-400,\n  5: $gray-500,\n  6: $gray-600,\n  7: $gray-700,\n  8: $gray-800,\n  9: $gray-900,\n) !default;\n\n$blues: (\n  0: $blue-000,\n  1: $blue-100,\n  2: $blue-200,\n  3: $blue-300,\n  4: $blue-400,\n  5: $blue-500,\n  6: $blue-600,\n  7: $blue-700,\n  8: $blue-800,\n  9: $blue-900,\n) !default;\n\n$greens: (\n  0: $green-000,\n  1: $green-100,\n  2: $green-200,\n  3: $green-300,\n  4: $green-400,\n  5: $green-500,\n  6: $green-600,\n  7: $green-700,\n  8: $green-800,\n  9: $green-900,\n) !default;\n\n$yellows: (\n  0: $yellow-000,\n  1: $yellow-100,\n  2: $yellow-200,\n  3: $yellow-300,\n  4: $yellow-400,\n  5: $yellow-500,\n  6: $yellow-600,\n  7: $yellow-700,\n  8: $yellow-800,\n  9: $yellow-900,\n) !default;\n\n$oranges: (\n  0: $orange-000,\n  1: $orange-100,\n  2: $orange-200,\n  3: $orange-300,\n  4: $orange-400,\n  5: $orange-500,\n  6: $orange-600,\n  7: $orange-700,\n  8: $orange-800,\n  9: $orange-900,\n) !default;\n\n$reds: (\n  0: $red-000,\n  1: $red-100,\n  2: $red-200,\n  3: $red-300,\n  4: $red-400,\n  5: $red-500,\n  6: $red-600,\n  7: $red-700,\n  8: $red-800,\n  9: $red-900,\n) !default;\n\n$purples: (\n  0: $purple-000,\n  1: $purple-100,\n  2: $purple-200,\n  3: $purple-300,\n  4: $purple-400,\n  5: $purple-500,\n  6: $purple-600,\n  7: $purple-700,\n  8: $purple-800,\n  9: $purple-900,\n) !default;\n\n$pinks: (\n  0: $pink-000,\n  1: $pink-100,\n  2: $pink-200,\n  3: $pink-300,\n  4: $pink-400,\n  5: $pink-500,\n  6: $pink-600,\n  7: $pink-700,\n  8: $pink-800,\n  9: $pink-900,\n) !default;\n\n$hue-maps: (\n  \"gray\": $grays,\n  \"blue\": $blues,\n  \"green\": $greens,\n  \"yellow\": $yellows,\n  \"orange\": $oranges,\n  \"red\": $reds,\n  \"purple\": $purples,\n  \"pink\": $pinks,\n) !default;\n","// Responsive media queries\n\n@mixin breakpoint($breakpoint) {\n  @if $breakpoint == \"\" {\n    @content;\n  }\n\n  @else {\n    // Retrieves the value from the key\n    $value: map-get($breakpoints, $breakpoint);\n\n    // If the key exists in the map\n    @if $value != null {\n      // Prints a media query based on the value\n      @media (min-width: $value) {\n        @content;\n      }\n    }\n\n    // If the key doesn't exist in the map\n    @else {\n      @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Please make sure it is defined in `$breakpoints` map.\";\n    }\n  }\n}\n\n// Retina media query\n\n@mixin retina-media-query {\n  @media\n    only screen and (-webkit-min-device-pixel-ratio: 2),\n    only screen and (min--moz-device-pixel-ratio: 2),\n    only screen and (-moz-min-device-pixel-ratio: 2),\n    only screen and (-o-min-device-pixel-ratio: 2/1),\n    only screen and (min-device-pixel-ratio: 2),\n    only screen and (min-resolution: 192dpi),\n    only screen and (min-resolution: 2dppx) {\n    @content;\n  }\n}\n\n// Clearfix\n//\n// Clears floats via mixin.\n\n@mixin clearfix {\n  &::before {\n    display: table;\n    content: \"\";\n  }\n\n  &::after {\n    display: table;\n    clear: both;\n    content: \"\";\n  }\n}\n","// Headings\n// --------------------------------------------------\n// stylelint-disable selector-max-type\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\nh1 { @include h1; }\nh2 { @include h2; }\nh3 { @include h3; }\nh4 { @include h4; }\nh5 { @include h5; }\nh6 { @include h6; }\n\n// Body text\n// --------------------------------------------------\n\np {\n  margin-top: 0;\n  margin-bottom: 10px;\n}\n\nsmall {\n  font-size: 90%;\n}\n\nblockquote {\n  margin: 0;\n}\n\n// Lists\n// --------------------------------------------------\n\nul,\nol {\n  padding-left: 0;\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\nol ol,\nul ol {\n  list-style-type: lower-roman;\n}\n\nul ul ol,\nul ol ol,\nol ul ol,\nol ol ol {\n  list-style-type: lower-alpha;\n}\n\ndd {\n  margin-left: 0;\n}\n\n// Code\n// --------------------------------------------------\n\ntt,\ncode {\n  font-family: $mono-font;\n  font-size: $font-size-small;\n}\n\npre {\n  margin-top: 0;\n  margin-bottom: 0;\n  font-family: $mono-font;\n  font-size: $font-size-small;\n}\n\n// Octicons\n// --------------------------------------------------\n\n// Move this over here as a temporary override to the octicons source repo\n// instead of updating that upstream.\n.octicon {\n  vertical-align: text-bottom;\n}\n","// Text hiding for image based text replacement.\n// Higher performance than -9999px because it only renders\n// the size of the actual text, not a full 9999px box.\n@mixin hide-text() {\n  overflow: hidden;\n  text-indent: 100%;\n  white-space: nowrap;\n}\n\n// Heading mixins for use within components\n// These match heading utilities in utilities/typography\n@mixin h1 {\n  font-size: $h1-size;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h2 {\n  font-size: $h2-size;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h3 {\n  font-size: $h3-size;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h4 {\n  font-size: $h4-size;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h5 {\n  font-size: $h5-size;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h6 {\n  font-size: $h6-size;\n  font-weight: $font-weight-bold;\n}\n\n// Responsive heading mixins\n// There are no responsive mixins for h4h6 because they are small\n// and don't need to be smaller on mobile.\n@mixin f1-responsive {\n  font-size: $h1-size-mobile;\n\n  // 32px on desktop\n  @include breakpoint(md) { font-size: $h1-size; }\n\n}\n\n@mixin f2-responsive {\n  font-size: $h2-size-mobile;\n\n  // 24px on desktop\n  @include breakpoint(md) { font-size: $h2-size; }\n}\n\n@mixin f3-responsive {\n  font-size: $h3-size-mobile;\n\n  // 20px on desktop\n  @include breakpoint(md) { font-size: $h3-size; }\n\n}\n\n// These use the mixins from above for responsive heading sizes.\n// The following mixins can be used where it's convenient or necessary to\n// couple the responsive font-size with the font-weight.\n@mixin h1-responsive {\n  @include f1-responsive;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h2-responsive {\n  @include f2-responsive;\n  font-weight: $font-weight-bold;\n}\n\n@mixin h3-responsive {\n  @include f3-responsive;\n  font-weight: $font-weight-bold;\n}\n","// Box\n// Intended to replace simple-box, boxed-group, and table-list\n\n.Box {\n  background-color: $white;\n  border: $border-width $border-style $border-gray-dark;\n  border-radius: $border-radius;\n}\n\n// Box padding density options\n.Box--condensed {\n  line-height: $lh-condensed;\n\n  .Box-header {\n    padding: $spacer-2 $spacer-3;\n  }\n\n  .Box-body {\n    padding: $spacer-2 $spacer-3;\n  }\n\n  .Box-footer {\n    padding: $spacer-2 $spacer-3;\n  }\n\n  .Box-btn-octicon {\n    &.btn-octicon {\n      padding: $spacer-2 $spacer-3;\n      margin: (-$spacer-2) (-$spacer-3);\n      line-height: $lh-condensed;\n    }\n  }\n\n  .Box-row {\n    padding: $spacer-2 $spacer-3;\n  }\n\n}\n\n.Box--spacious {\n  .Box-header {\n    padding: $spacer-4;\n    line-height: $lh-condensed;\n  }\n\n  .Box-title {\n    font-size: $h3-size;\n  }\n\n  .Box-body {\n    padding: $spacer-4;\n  }\n\n  .Box-footer {\n    padding: $spacer-4;\n  }\n\n  .Box-btn-octicon {\n    &.btn-octicon {\n      padding: $spacer-4;\n      margin: (-$spacer-4) (-$spacer-4);\n    }\n  }\n\n  .Box-row {\n    padding: $spacer-4;\n  }\n\n}\n\n.Box-header {\n  padding: $spacer-3;\n  margin: (-$border-width) (-$border-width) 0;\n  background-color: $bg-gray;\n  border-color: $border-gray-dark;\n  border-style: $border-style;\n  border-width: $border-width;\n  border-top-left-radius: $border-radius;\n  border-top-right-radius: $border-radius;\n}\n\n.Box-title {\n  font-size: $body-font-size;\n  font-weight: $font-weight-bold;\n}\n\n.Box-body {\n  padding: $spacer-3;\n  border-bottom: $border-width $border-style $border-gray;\n\n  // Ensures bottom-border doesn't poke out when .Box-body used without box-footer\n  &:last-of-type {\n    margin-bottom: -$border-width;\n    border-bottom-right-radius: 2px;\n    border-bottom-left-radius: 2px;\n  }\n}\n\n// Box rows\n.Box-row {\n  padding: $spacer-3;\n  margin-top: -1px;\n  list-style-type: none; // To account for applying Box component to a list\n  border-top: $border-width $border-style $border-gray;\n\n  &:first-of-type {\n    border-top-color: transparent;\n    border-top-left-radius: 2px;\n    border-top-right-radius: 2px;\n  }\n\n  &:last-of-type {\n    border-bottom-right-radius: 2px;\n    border-bottom-left-radius: 2px;\n  }\n\n  // Adds a blue vertical line to the left of the row\n  // For indicating a row item is unread\n  &.Box-row--unread,\n  // .unread to be deprecated with .Box-row-unread\n  &.unread {\n    box-shadow: 2px 0 0 $blue inset;\n  }\n\n  &.navigation-focus {\n    // Focus styles for when drag icon\n    .Box-row--drag-button {\n      color: $text-blue;\n      cursor: grab;\n      opacity: 100;\n    }\n\n    // Grabbing while row is dragged\n    &.is-dragging .Box-row--drag-button {\n      cursor: grabbing;\n    }\n\n    // Row dragging styles\n    &.sortable-chosen {\n      background-color: $bg-gray-light;\n    }\n\n    // Makes dragging row background gray\n    &.sortable-ghost {\n      background-color: $bg-gray;\n\n      // Hides contents of row while dragging so row looks solid gray\n      .Box-row--drag-hide {\n        opacity: 0;\n      }\n    }\n\n  }\n}\n\n.Box-row--focus-gray {\n  &.navigation-focus {\n    background-color: $bg-gray;\n  }\n}\n\n.Box-row--focus-blue {\n  &.navigation-focus {\n    background-color: $bg-blue-light;\n  }\n}\n\n.Box-row--hover-gray {\n  &:hover {\n    background-color: $bg-gray;\n  }\n}\n\n.Box-row--hover-blue {\n  &:hover {\n    background-color: $bg-blue-light;\n  }\n}\n\n// Optional link style\n// Makes links on mobile blue since they don't have hover state,\n// and links are dark-gray with blue hover on desktop.\n.Box-row-link {\n  @include breakpoint(md) {\n\n    color: $text-gray-dark;\n    text-decoration: none;\n\n    &:hover {\n      color: $text-blue;\n      text-decoration: none;\n    }\n\n  }\n}\n\n// Optional drag icon styles for reordering items\n// Focus styles included in .Box-row above\n.Box-row--drag-button {\n  opacity: 0;\n}\n\n.Box-footer {\n  padding: $spacer-3;\n  margin-top: -1px; // prevents double border when used with .Box-body\n  border-top: $border-width $border-style $border-gray;\n}\n\n// Option for a box with scrolling content\n.Box--scrollable {\n  max-height: 324px;\n  overflow: scroll;\n}\n\n// Box themes\n\n.Box--blue {\n  border-color: $border-blue-light;\n\n  .Box-header {\n    background-color: $bg-blue-light;\n    border-color: $border-blue-light;\n  }\n\n  .Box-body {\n    border-color: $border-blue-light;\n  }\n\n  .Box-row {\n    border-color: $border-blue-light;\n  }\n\n  .Box-footer {\n    border-color: $border-blue-light;\n  }\n}\n\n// Applies and red border to the outside of the box,\n// but not to the border separating rows.\n.Box--danger {\n  border-color: $border-red;\n\n  .Box-row {\n    &:first-of-type {\n      border-color: $border-red;\n    }\n  }\n\n  .Box-body {\n    &:last-of-type {\n      border-color: $border-red;\n    }\n  }\n}\n\n.Box-header--blue {\n  background-color: $bg-blue-light;\n  border-color: $border-blue-light;\n}\n\n// Box row highlight themes\n\n.Box-row--yellow {\n  background-color: $yellow-100;\n}\n\n.Box-row--blue {\n  background-color: $bg-blue-light;\n}\n\n.Box-row--gray {\n  background-color: $bg-gray;\n}\n\n//Box with btn-octicon\n.Box-btn-octicon {\n\n  // Increase specificity when btn-octicon is used because comes after .Box in the cascade\n  &.btn-octicon {\n    padding: $spacer-3 $spacer-3;\n    margin: (-$spacer-3) (-$spacer-3);\n    line-height: $lh-default; // override btn-octicon line-height\n  }\n}\n","// Miscellaneous variables\n// stylelint-disable declaration-bang-space-before\n\n// Border size\n$border-width: 1px !default;\n$border-color: $border-gray !default;\n$border-style: solid !default;\n$border: $border-width $border-color $border-style !default;\n$border-radius: 3px !default;\n\n// Custom explore grid border\n$exploregrid-item-border-radius: 4px;\n\n// Box shadow\n$box-shadow: 0 1px 1px rgba($black, 0.1) !default;\n$box-shadow-medium: 0 1px 5px $black-fade-15 !default;\n$box-shadow-large: 0 1px 15px $black-fade-15 !default;\n$box-shadow-extra-large: 0 10px 50px rgba($black, 0.07) !default;\n\n// Tooltips\n$tooltip-max-width: 250px !default;\n$tooltip-background-color: $black;\n$tooltip-text-color: $white !default;\n$tooltip-delay: 0.4s !default;\n$tooltip-duration: 0.1s !default;\n\n// Should be moved into custom github css\n$stats-switcher-py: 10px;\n// Future proof this `height` value by finding the computed line-height, then\n// adding the total value of the vertical padding. This var is used to toggle\n// between the stats bar and language breakdown.\n$stats-viewport-height: ($body-font-size * $body-line-height) + ($stats-switcher-py * 2);\n\n$min_tab_size: 1;\n$max_tab_size: 12;\n\n// Button and form variables\n$form-control-shadow: inset 0 1px 2px rgba($black, 0.075);\n$btn-input-focus-shadow: 0 0 0 0.2em rgba($blue, 0.3);\n$btn-active-shadow: inset 0 0.15em 0.3em $black-fade-15;\n","// Layout variables\n// stylelint-disable declaration-bang-space-before\n\n// These are our margin and padding utility spacers. The default step size we\n// use is 8px. This gives us a key of:\n//    0 => 0px\n//    1 => 4px\n//    2 => 8px\n//    3 => 16px\n//    4 => 24px\n//    5 => 32px\n//    6 => 40px\n$spacer: 8px !default;\n\n// Our spacing scale\n$spacer-0: 0 !default;                  // 0\n$spacer-1: round($spacer / 2) !default; // 4px\n$spacer-2: $spacer !default;            // 8px\n$spacer-3: $spacer * 2 !default;        // 16px\n$spacer-4: $spacer * 3 !default;        // 24px\n$spacer-5: $spacer * 4 !default;        // 32px\n$spacer-6: $spacer * 5 !default;        // 40px\n\n// The list of spacer values\n$spacers: (\n  $spacer-0,\n  $spacer-1,\n  $spacer-2,\n  $spacer-3,\n  $spacer-4,\n  $spacer-5,\n  $spacer-6,\n) !default;\n\n// And the map of spacers, for easier looping:\n// @each $scale, $length in $spacer-map { ... }\n$spacer-map: (\n  0: $spacer-0,\n  1: $spacer-1,\n  2: $spacer-2,\n  3: $spacer-3,\n  4: $spacer-4,\n  5: $spacer-5,\n  6: $spacer-6,\n) !default;\n\n// Em spacer variables\n$em-spacer-1: 0.0625em !default; // 1/16\n$em-spacer-2: 0.125em !default;  // 1/8\n$em-spacer-3: 0.25em !default;   // 1/4\n$em-spacer-4: 0.375em !default;  // 3/8\n$em-spacer-5: 0.5em !default;    // 1/2\n$em-spacer-6: 0.75em !default;   // 3/4\n\n// Fixed-width container variables\n$container-width: 980px !default;\n$grid-gutter:     10px !default;\n\n// Breakpoint widths\n$width-xs: 0 !default;\n// Small screen / phone\n$width-sm: 544px !default;\n// Medium screen / tablet\n$width-md: 768px !default;\n// Large screen / desktop (980 + (16 * 2)) <= container + gutters\n$width-lg: 1012px !default;\n// Extra large screen / wide desktop\n$width-xl: 1280px !default;\n\n// Responsive container widths\n$container-sm: $width-sm !default;\n$container-md: $width-md !default;\n$container-lg: $width-lg !default;\n$container-xl: $width-xl !default;\n\n// Breakpoints in the form (name: length)\n$breakpoints: (\n  sm: $width-sm,\n  md: $width-md,\n  lg: $width-lg,\n  xl: $width-xl\n) !default;\n\n// This map in the form (breakpoint: variant) is used to iterate over\n// breakpoints and create both responsive and non-responsive classes in one\n// loop:\n//\n// ```scss\n// @each $breakpoint, $variant of $responsive-variants {\n//   @include breakpoint($breakpoint) {\n//     .foo#{$variant}-bar { foo: bar !important; }\n//   }\n// }\n// ```\n$responsive-variants: (\n  \"\": \"\",\n  sm: \"-sm\",\n  md: \"-md\",\n  lg: \"-lg\",\n  xl: \"-xl\",\n) !default;\n\n// responive utility position values\n$responsive-positions: (\n  static,\n  relative,\n  absolute,\n  fixed\n) !default;\n",".breadcrumb-item {\n  display: inline-block;\n  margin-left: -0.35em;\n  white-space: nowrap;\n  list-style: none;\n\n  &::after {\n    padding-right: $em-spacer-5;\n    padding-left: $em-spacer-5;\n    color: $border-gray;\n    content: \"/\";\n  }\n\n  &:first-child {\n    margin-left: 0;\n  }\n}\n\n.breadcrumb-item-selected {\n  &::after {\n    content: none;\n  }\n}\n","// stylelint-disable selector-max-type, block-opening-brace-space-before, no-duplicate-selectors\n\n// Base button styles\n.btn {\n  position: relative;\n  display: inline-block;\n  padding: 6px 12px;\n  font-size: $body-font-size;\n  font-weight: $font-weight-bold;\n  line-height: 20px; // Specifically not inherit our `<body>` default\n  white-space: nowrap;\n  vertical-align: middle;\n  cursor: pointer;\n  user-select: none;\n  background-repeat: repeat-x;\n  background-position: -1px -1px;\n  background-size: 110% 110%;\n  border: 1px solid transparentize($black, 0.8);\n  border-radius: 0.25em;\n  appearance: none; // Corrects inability to style clickable `input` types in iOS.\n\n  i {\n    font-style: normal;\n    font-weight: $font-weight-semibold;\n    opacity: 0.75;\n  }\n\n  .octicon {\n    vertical-align: text-top;\n  }\n\n  // Darken for just a tad more contrast against the button background\n  .Counter {\n    color: $gray-600;\n    text-shadow: none;\n    background-color: rgba($black, 0.1);\n  }\n\n  &:hover {\n    text-decoration: none;\n    background-repeat: repeat-x;\n  }\n\n  &:focus {\n    outline: 0;\n  }\n\n  &:disabled,\n  &.disabled {\n    cursor: default;\n    // Repeat `background-position` because `:hover`\n    background-position: 0 0;\n  }\n\n  &:active,\n  &.selected {\n    background-image: none;\n  }\n}\n\n.btn         { @include btn-solid($text-gray-dark, $gray-000, darken($gray-100, 2%)); }\n.btn-primary { @include btn-solid($text-white, $green-400, $green-500); }\n@warn \".btn-purple will be removed in 13.0.0. Please don't make buttons purple.\";\n.btn-purple  { @include btn-solid($text-white, lighten($purple-500, 5%), darken($purple-500, 5%)); }\n.btn-blue    { @include btn-solid($text-white, lighten($blue-500, 8%), darken($blue-500, 2%)); }\n.btn-danger  { @include btn-inverse($red-600, $gray-000, darken($gray-100, 2%)); }\n\n// Outline button\n//\n// For when we need a linky-action that's not too heavy in busier\n// areas like conversation timelines.\n.btn-outline {\n  @include btn-outline($text-blue);\n}\n\n// Social button count\n.btn-with-count {\n  float: left;\n  border-top-right-radius: 0;\n  border-bottom-right-radius: 0;\n}\n\n// Minibutton overrides\n//\n// Tweak `line-height` to make them smaller.\n.btn-sm {\n  padding: 3px 10px;\n  font-size: $font-size-small;\n  line-height: 20px;\n}\n\n// Large button adds more padding around text. Use font-size utils to increase font-size.. e.g, <p class=\"text-gamma\"><button class=\"btn btn-large btn-primary\" type=\"button\">Big green button</button></p>\n.btn-large {\n  padding: $em-spacer-6 1.25em;\n  font-size: inherit;\n  border-radius: 6px;\n}\n\n// Hidden text button\n//\n// Use `.hidden-text-expander` to indicate and expand hidden text.\n.hidden-text-expander {\n  display: block;\n\n  &.inline {\n    position: relative;\n    top: -1px;\n    display: inline-block;\n    margin-left: 5px;\n    line-height: 0;\n  }\n}\n\n.hidden-text-expander a,\n.ellipsis-expander {\n  display: inline-block;\n  height: 12px;\n  padding: 0 5px 5px;\n  font-size: $font-size-small;\n  font-weight: $font-weight-bold;\n  line-height: 6px;\n  color: $gray-700;\n  text-decoration: none;\n  vertical-align: middle;\n  background: lighten($gray-300, 5%);\n  border: 0;\n  border-radius: 1px;\n\n  &:hover {\n    text-decoration: none;\n    background-color: darken($gray-300, 4%);\n  }\n\n  &:active {\n    color: $text-white;\n    background-color: $blue-400;\n  }\n}\n\n// Social count bubble\n//\n// A container that is used for social bubbles counts.\n.social-count {\n  float: left;\n  padding: 3px 10px;\n  font-size: $font-size-small;\n  font-weight: $font-weight-bold;\n  line-height: 20px;\n  color: $text-gray-dark;\n  vertical-align: middle;\n  background-color: $bg-white;\n  border: 1px solid transparentize($black, 0.8); // match with .btn\n  border-left: 0;\n  border-top-right-radius: 3px;\n  border-bottom-right-radius: 3px;\n\n  &:hover,\n  &:active {\n    text-decoration: none;\n  }\n\n  &:hover {\n    color: $text-blue;\n    cursor: pointer;\n  }\n}\n\n// Full-width button\n//\n// These buttons expand to the full width of their parent container\n.btn-block {\n  display: block;\n  width: 100%;\n  text-align: center;\n}\n\n// Link-like buttons\n//\n// This class is for styling <button> and <input> elements to look like links\n.btn-link {\n  display: inline-block;\n  padding: 0;\n  font-size: inherit;\n  color: $text-blue;\n  text-decoration: none;\n  white-space: nowrap;\n  cursor: pointer;\n  user-select: none;\n  background-color: transparent;\n  border: 0;\n  appearance: none; // Corrects inability to style clickable `input` types in iOS.\n\n  &:hover {\n    text-decoration: underline;\n  }\n\n  &:disabled {\n    &,\n    &:hover {\n      color: rgba($gray-600, 0.5);\n      cursor: default;\n    }\n  }\n}\n\n.details-reset {\n  // Remove marker added by the display: list-item browser default\n  > summary { list-style: none; }\n  // Remove marker added by details polyfill\n  > summary::before { display: none; }\n  // Remove marker added by Chrome\n  > summary::-webkit-details-marker { display: none; }\n}\n","// stylelint-disable block-closing-brace-newline-after\n\n// Button color generator for primary and themed buttons\n\n// New button hotness\n@mixin btn-solid($color, $bg, $bg2) {\n  color: $color;\n  background-color: $bg2;\n  background-image: linear-gradient(-180deg, $bg 0%, $bg2 90%);\n\n  @if $bg == $gray-000 {\n    &:focus,\n    &.focus {\n      box-shadow: $btn-input-focus-shadow;\n    }\n\n    &:hover,\n    &.hover {\n      background-color: darken($bg2, 3%);\n      background-image: linear-gradient(-180deg, darken($bg, 3%) 0%, darken($bg2, 3%) 90%);\n      background-position: 0 -$em-spacer-5;\n      border-color: rgba($black, 0.35);\n    }\n\n    &:active,\n    &.selected,\n    [open] > & {\n      background-color: darken(desaturate($bg, 10%), 6%);\n      background-image: none;\n      border-color: rgba($black, 0.35); // repeat to avoid shift on click-drag off of button\n      box-shadow: $btn-active-shadow;\n    }\n\n    &:disabled,\n    &.disabled {\n      color: rgba($color, 0.4);\n      background-color: $bg2;\n      background-image: none;\n      border-color: transparentize($black, 0.8); // back to default .btn\n      box-shadow: none;\n    }\n\n  } @else {\n    &:focus,\n    &.focus {\n      box-shadow: 0 0 0 0.2em rgba($bg, 0.4);\n    }\n\n    &:hover,\n    &.hover {\n      background-color: darken($bg2, 2%);\n      background-image: linear-gradient(-180deg, darken($bg, 2%) 0%, darken($bg2, 2%) 90%);\n      background-position: 0 -$em-spacer-5;\n      border-color: $black-fade-50;\n    }\n\n    &:active,\n    &.selected,\n    [open] > & {\n      background-color: darken(mix($bg, $bg2, 50%), 7%);\n      background-image: none;\n      border-color: $black-fade-50; // repeat to avoid shift on click-drag off of button\n      box-shadow: $btn-active-shadow;\n    }\n\n    &:disabled,\n    &.disabled {\n      color: rgba($color, 0.75);\n      background-color: mix($bg2, $white, 50%);\n      background-image: none;\n      border-color: transparentize($black, 0.8); // repeat .btn default to avoid shift on click-drag off of button\n      box-shadow: none;\n    }\n\n    .Counter {\n      color: darken($bg, 8%);\n      background-color: $white;\n    }\n  }\n}\n\n// Inverse button hover style\n@mixin btn-inverse($color, $bg, $bg2) {\n  color: $color;\n  background-color: $bg;\n  background-image: linear-gradient(-180deg, $bg 0%, $bg2 90%);\n\n  &:focus {\n    box-shadow: 0 0 0 0.2em rgba($color, 0.4);\n  }\n\n  &:hover {\n    color: $text-white;\n    background-color: $color;\n    background-image: linear-gradient(-180deg, lighten($color, 10%) 0%, $color 90%);\n    border-color: $black-fade-50;\n\n    .Counter {\n      color: $text-white;\n    }\n  }\n\n  &:active,\n  &.selected,\n  [open] > & {\n    color: $text-white;\n    background-color: darken($color, 5%);\n    background-image: none;\n    border-color: $black-fade-50;\n    box-shadow: $btn-active-shadow;\n  }\n\n  &:disabled,\n  &.disabled {\n    color: rgba($color, 0.4);\n    background-color: $bg2;\n    background-image: none;\n    border-color: transparentize($black, 0.8); // back to default .btn\n    box-shadow: none;\n  }\n}\n\n// Outline color generator for btn-outline to make the hover state inverse the text and bg colors.\n@mixin btn-outline($text-color: $text-blue, $bg-color: $bg-white) {\n  color: $text-color;\n  background-color: $bg-color;\n  background-image: none;\n\n  .Counter {\n    background-color: rgba($black, 0.07);\n  }\n\n  &:hover,\n  &:active,\n  &.selected,\n  [open] > & {\n    color: $bg-color;\n    background-color: $text-color;\n    background-image: none;\n    border-color: $text-color;\n\n    .Counter {\n      color: $text-color;\n      background-color: $bg-color;\n    }\n  }\n\n  &:focus {\n    border-color: $text-color;\n    box-shadow: 0 0 0 0.2em rgba($text-color, 0.4);\n  }\n\n  &:disabled,\n  &.disabled {\n    color: $black-fade-30;\n    background-color: $bg-white;\n    border-color: $black-fade-15;\n    box-shadow: none;\n  }\n}\n","// Button group\n//\n// A button group is a series of buttons laid out next to each other, all part\n// of one visual button, but separated by rules to be separate.\n\n.BtnGroup {\n  display: inline-block;\n  vertical-align: middle;\n  @include clearfix();\n\n  // Proper spacing for multiple button groups (a la, gollum editor)\n  + .BtnGroup,\n  + .btn {\n    margin-left: $spacer-1;\n  }\n}\n\n.BtnGroup-item {\n  position: relative;\n  float: left;\n  border-right-width: 0;\n  border-radius: 0;\n\n  &:first-child {\n    border-top-left-radius: $border-radius;\n    border-bottom-left-radius: $border-radius;\n  }\n\n  &:last-child {\n    border-right-width: $border-width;\n    border-top-right-radius: $border-radius;\n    border-bottom-right-radius: $border-radius;\n  }\n\n  &.selected,\n  &:focus,\n  &:active,\n  &:hover {\n    border-right-width: $border-width;\n\n    + .BtnGroup-item,\n    + .BtnGroup-parent .BtnGroup-item {\n      border-left-width: 0;\n    }\n  }\n}\n\n.BtnGroup-parent {\n  float: left;\n\n  &:first-child .BtnGroup-item {\n    border-top-left-radius: $border-radius;\n    border-bottom-left-radius: $border-radius;\n  }\n\n  &:last-child .BtnGroup-item {\n    border-right-width: $border-width;\n    border-top-right-radius: $border-radius;\n    border-bottom-right-radius: $border-radius;\n  }\n\n  .BtnGroup-item {\n    border-right-width: 0;\n    border-radius: 0;\n  }\n\n  &.selected,\n  &:focus,\n  &:active,\n  &:hover {\n    .BtnGroup-item {\n      border-right-width: $border-width;\n    }\n\n    + .BtnGroup-item,\n    + .BtnGroup-parent .BtnGroup-item {\n      border-left-width: 0;\n    }\n  }\n}\n\n// ensure that the focus ring sits above the adjacent buttons\n.BtnGroup-item,\n.BtnGroup-parent {\n  &:focus,\n  &:active {\n    z-index: 1;\n  }\n}\n","// TableObject is a module for creating dynamically resizable elements that\n// always sit on the same horizontal line (e.g., they never wrap). Using\n// tables means it's cross browser friendly.\n\n.TableObject {\n  display: table;\n}\n\n// Place this on every \"cell\"\n.TableObject-item {\n  display: table-cell;\n  width: 1%;\n  white-space: nowrap;\n  vertical-align: middle;\n}\n\n// Place this on the largest or most important \"cell\"\n.TableObject-item--primary {\n  width: 99%;\n}\n","// Needs refactoring\n// stylelint-disable selector-no-qualifying-type, selector-max-compound-selectors\n// stylelint-disable selector-max-type\n// Base form controls\n//\n// Overrides for common inputs for easier styling.\n\nfieldset {\n  padding: 0;\n  margin: 0;\n  border: 0;\n}\n\nlabel {\n  font-weight: $font-weight-bold;\n}\n\n.form-control,\n.form-select {\n  min-height: 34px;\n  padding: 6px $spacer-2;\n  font-size: $h4-size;\n  line-height: 20px;\n  color: $text-gray-dark;\n  vertical-align: middle;\n  background-color: $bg-white;\n  background-repeat: no-repeat; // Repeat and position set for form states (success, error, etc)\n  background-position: right 8px center; // For form validation. This keeps images 8px from right and centered vertically.\n  border: 1px solid $border-gray-dark;\n  border-radius: 3px;\n  outline: none;\n  box-shadow: $form-control-shadow;\n\n  &.focus,\n  &:focus {\n    border-color: $blue-400;\n    outline: none;\n    box-shadow: $form-control-shadow, $btn-input-focus-shadow;\n  }\n\n  // Ensures inputs don't zoom on mobile but are body-font size on desktop\n  @include breakpoint(md) {\n    font-size: $body-font-size;\n  }\n}\n\n// Inputs with contrast for easy light gray backgrounds against white.\n.input-contrast {\n  background-color: $bg-gray-light;\n\n  &:focus { background-color: $bg-white; }\n}\n\n// Custom styling for HTML5 validation bubbles (WebKit only)\n::placeholder {\n  color: $text-gray-light;\n}\n\n// Mini inputs, to match .minibutton\n.input-sm {\n  min-height: 28px;\n  padding-top: 3px;\n  padding-bottom: 3px;\n  font-size: $font-size-small;\n  line-height: 20px;\n}\n\n// Large inputs\n.input-lg {\n  padding: $spacer-1 10px;\n  font-size: $h4-size;\n}\n\n// Full-width inputs\n.input-block {\n  display: block;\n  width: 100%;\n}\n\n// Inputs with monospace text\n.input-monospace {\n  font-family: $mono-font;\n}\n\n// Hide the icon that tries to autofill contact info in webkit\n.input-hide-webkit-autofill {\n  &::-webkit-contacts-auto-fill-button {\n    position: absolute;\n    right: 0;\n    display: none !important;\n    pointer-events: none;\n    visibility: hidden;\n  }\n}\n\n// Checkboxes and Radiobuttons\n//\n// For checkboxes and radio button selections.\n.form-checkbox {\n  padding-left: 20px;\n  margin: 15px 0;\n  vertical-align: middle;\n\n  label {\n    em.highlight {\n      position: relative;\n      left: -$spacer-1;\n      padding: 2px $spacer-1;\n      font-style: normal;\n      background: $yellow-100;\n      border-radius: 3px;\n    }\n  }\n\n  input[type=checkbox],\n  input[type=radio] {\n    float: left;\n    margin: 5px 0 0 -20px;\n    vertical-align: middle;\n  }\n\n  .note {\n    display: block;\n    margin: 0;\n    font-size: $font-size-small;\n    font-weight: $font-weight-normal;\n    color: $gray-600;\n  }\n}\n\n.form-checkbox-details {\n  display: none;\n}\n\n.form-checkbox-details-trigger {\n  &:checked {\n    // child of sibling or sibling\n    ~ * .form-checkbox-details,\n    ~ .form-checkbox-details { // eslint-disable selector-combinator-space-before\n      display: block;\n    }\n  }\n}\n\n// Field groups\n//\n// Wrap field groups in `<div.hfields>` to lay them out horizontally - great for\n// the top of pages with autosave.\n.hfields {\n  margin: 15px 0;\n  @include clearfix;\n\n  .form-group {\n    float: left;\n    margin: 0 30px 0 0;\n\n    dt {\n      label {\n        display: inline-block;\n        margin: 5px 0 0;\n        color: $gray-600;\n      }\n\n      img {\n        position: relative;\n        top: -2px;\n      }\n    }\n  }\n\n  .btn {\n    float: left;\n    margin: 28px 25px 0 -20px;\n  }\n\n  .form-select { margin-top: 5px; }\n}\n\n// Hide the up/down buttons in <input type=\"number\"> in the login form, the\n// input is used for two-factor auth codes, type=\"number\" makes it more usable\n// on phones\ninput::-webkit-outer-spin-button,\ninput::-webkit-inner-spin-button {\n  margin: 0;\n  appearance: none;\n}\n\n// Input groups\n\n.form-actions {\n  @include clearfix;\n\n  .btn {\n    float: right;\n\n    + .btn {\n      margin-right: 5px;\n    }\n  }\n}\n\n.form-warning {\n  padding: $spacer-2 10px;\n  margin: 10px 0;\n  font-size: $h5-size;\n  color: $yellow-900;\n  background: $yellow-100;\n  border: 1px solid $border-yellow;\n  border-radius: 3px;\n\n  p {\n    margin: 0;\n    line-height: $lh-default;\n  }\n\n  a { font-weight: $font-weight-bold; }\n}\n","@import \"color-system.scss\";\n// Color variables\n// stylelint-disable declaration-bang-space-before\n\n// State indicators.\n$status-pending:    desaturate($yellow-700, 15%) !default;\n\n// Repository type colors\n// Should be able to deprecate these in future\n$repo-private-text: $black-fade-70 !default;\n$repo-private-bg:   $yellow-000 !default;\n$repo-private-icon: transparentize($yellow-900, 0.5) !default;\n\n// Highlight used for things like search\n$highlight-yellow: rgba(255, 247, 140, 0.5);\n\n// Border colors\n$border-black-fade:  $black-fade-15 !default;\n$border-white-fade:  $white-fade-15 !default;\n\n$border-blue:        $blue-500 !default;\n$border-blue-light:  $blue-200 !default;\n\n$border-green:       $green-400 !default;\n$border-green-light: desaturate($green-300, 40%) !default;\n\n$border-purple:      $purple !default;\n\n$border-red:         $red !default;\n$border-red-light:   desaturate($red-300, 60%) !default;\n\n$border-yellow:      desaturate($yellow-300, 60%) !default;\n\n$border-gray-dark:   $gray-300 !default;\n$border-gray-darker: $gray-700 !default;\n$border-gray-light:  lighten($gray-200, 3%) !default;\n$border-gray:        $gray-200 !default;\n\n// Background colors\n$bg-blue-light:     $blue-000 !default;\n$bg-blue:           $blue-500 !default;\n$bg-gray-dark:      $gray-900 !default;\n$bg-gray-light:     $gray-000 !default;\n$bg-gray:           $gray-100 !default;\n$bg-green:          $green-500 !default;\n$bg-green-light:    $green-100 !default;\n$bg-orange:         $orange-700 !default;\n$bg-purple:         $purple-500 !default;\n$bg-purple-light:   $purple-000 !default;\n$bg-pink:           $pink-500 !default;\n$bg-red:            $red-500 !default;\n$bg-red-light:      $red-100 !default;\n$bg-white:          $white !default;\n$bg-yellow:         $yellow-500 !default;\n$bg-yellow-light:   $yellow-200 !default;\n$bg-yellow-dark:    $yellow-700 !default;\n\n// Text colors\n$text-blue:         $blue-500 !default;\n$text-gray-dark:    $gray-900 !default;\n$text-gray-light:   $gray-500 !default;\n$text-gray:         $gray-600 !default;\n$text-green:        $green-500 !default;\n$text-orange:       $orange-900 !default;\n$text-orange-light: $orange-600 !default;\n$text-purple:       $purple !default;\n$text-pink:         $pink-500 !default;\n$text-red:          $red-600 !default;\n$text-white:        $white !default;\n$text-yellow:       $yellow-800 !default;\n","// Custom select\n//\n// Apply `.select` to any `<select>` element for custom styles.\n.form-select {\n  display: inline-block;\n  max-width: 100%;\n  height: 34px;\n  padding-right: $spacer-4;\n  background: $bg-white url(\"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAUCAMAAACzvE1FAAAADFBMVEUzMzMzMzMzMzMzMzMKAG/3AAAAA3RSTlMAf4C/aSLHAAAAPElEQVR42q3NMQ4AIAgEQTn//2cLdRKppSGzBYwzVXvznNWs8C58CiussPJj8h6NwgorrKRdTvuV9v16Afn0AYFOB7aYAAAAAElFTkSuQmCC\") no-repeat right 8px center;\n  background-size: 8px 10px;\n  appearance: none;\n\n  // Hides the default caret in IE11\n  &::-ms-expand {\n    opacity: 0;\n  }\n\n  &[multiple] {\n    height: auto;\n  }\n}\n\n.select-sm {\n  height: 28px;\n  min-height: 28px;\n  padding-top: 3px;\n  padding-bottom: 3px;\n  font-size: $font-size-small;\n\n  &[multiple] {\n    height: auto;\n    min-height: 0;\n  }\n}\n","// Form groups\n//\n// Typical form groups - `<dl.form-group>` with a `<dt>` containing the label and\n// `<dd> containing the form elements.\n// stylelint-disable selector-max-compound-selectors\n// stylelint-disable selector-max-type\n.form-group {\n  margin: 15px 0;\n\n  // Text fields\n  .form-control {\n    width: 440px;\n    max-width: 100%;\n    margin-right: 5px;\n    background-color: $bg-gray-light;\n\n    &:focus {\n      background-color: $bg-white;\n    }\n\n    &.shorter { width: 130px; }\n\n    &.short { width: 250px; }\n\n    &.long { width: 100%; }\n  }\n\n  // Textarea\n  // stylelint-disable selector-no-qualifying-type\n  textarea.form-control {\n    width: 100%;\n    height: 200px;\n    min-height: 200px;\n\n    &.short {\n      height: 50px;\n      min-height: 50px;\n    }\n  }\n  // stylelint-enable selector-no-qualifying-type\n\n  // The Label\n  dt {\n    margin: 0 0 6px;\n  }\n\n  label {\n    position: relative;\n  }\n\n  &.flattened dt {\n    float: left;\n    margin: 0;\n    line-height: 32px;\n  }\n\n  &.flattened dd {\n    line-height: 32px;\n  }\n\n  //\n  // Form Elements\n  //\n  // stylelint-disable selector-no-qualifying-type\n  dd {\n    h4 {\n      margin: $spacer-1 0 0;\n\n      &.is-error { color: $text-red; }\n\n      &.is-success { color: $text-green; }\n\n      + .note {\n        margin-top: 0;\n      }\n    }\n  }\n  // stylelint-enable selector-no-qualifying-type\n\n  //\n  // Variants\n  //\n\n  &.required {\n    dt label::after {\n      padding-left: 5px;\n      color: $text-red;\n      content: \"*\";\n    }\n  }\n\n  // Form AJAX states\n  //\n  // Form fields that need feedback for AJAX loading, success\n  // states and errored states.\n  .success,\n  .error,\n  .indicator {\n    display: none;\n    font-size: $font-size-small;\n    font-weight: $font-weight-bold;\n  }\n\n  &.loading {\n    opacity: 0.5;\n\n    .indicator {\n      display: inline;\n    }\n\n    .spinner {\n      display: inline-block;\n      vertical-align: middle;\n    }\n  }\n\n  &.successful {\n    .success {\n      display: inline;\n      color: $text-green;\n    }\n  }\n\n  // Form validation\n  //\n  // Our inline errors\n\n  &.warn,\n  &.errored {\n    .warning,\n    .error {\n      position: absolute;\n      z-index: 10;\n      display: block; // Show up in errored/warn state\n      max-width: 450px; // Keep our long errors readable\n      padding: 5px $spacer-2;\n      margin: $spacer-1 0 0;\n      font-size: 13px;\n      font-weight: $font-weight-normal;\n      border-style: solid;\n      border-width: 1px;\n      border-radius: 3px;\n\n      &::after,\n      &::before {\n        position: absolute;\n        bottom: 100%;\n        left: 10px;\n        z-index: 15;\n        width: 0;\n        height: 0;\n        pointer-events: none;\n        content: \" \";\n        border: solid transparent;\n      }\n\n      &::after {\n        border-width: 5px;\n      }\n\n      &::before {\n        margin-left: -1px;\n        border-width: 6px;\n      }\n    }\n  }\n\n  &.warn {\n    .warning {\n      color: $yellow-900;\n      background-color: $yellow-100;\n      border-color: $border-yellow;\n\n      &::after {\n        border-bottom-color: $yellow-100;\n      }\n\n      &::before {\n        border-bottom-color: $border-yellow;\n      }\n    }\n  }\n\n  &.errored {\n    label {\n      color: $text-red;\n    }\n\n    .error {\n      color: $red-900;\n      background-color: $bg-red-light;\n      border-color: $border-red-light;\n\n      &::after {\n        border-bottom-color: $bg-red-light;\n      }\n\n      &::before {\n        border-bottom-color: $border-red-light;\n      }\n    }\n  }\n}\n\n.note {\n  min-height: 17px;\n  margin: $spacer-1 0 2px;\n  font-size: $font-size-small;\n  color: $text-gray;\n\n  .spinner {\n    margin-right: 3px;\n    vertical-align: middle;\n  }\n}\n","// Needs refactoring\n// stylelint-disable selector-no-qualifying-type, selector-max-type\ndl.form-group > dd {\n  .form-control {\n    &.is-autocheck-loading,\n    &.is-autocheck-successful,\n    &.is-autocheck-errored {\n      padding-right: 30px; // Leave some space for our validation icons\n    }\n\n    &.is-autocheck-loading {\n      background-image: url(\"/images/spinners/octocat-spinner-16px.gif\");\n    }\n\n    &.is-autocheck-successful {\n      background-image: url(\"/images/modules/ajax/success.png\");\n    }\n\n    &.is-autocheck-errored {\n      background-image: url(\"/images/modules/ajax/error.png\");\n    }\n  }\n}\n\n// Retinization of form validation icons that aren't octicons yet\n@include retina-media-query {\n  dl.form-group > dd {\n    .form-control {\n      &.is-autocheck-loading,\n      &.is-autocheck-successful,\n      &.is-autocheck-errored {\n        background-size: 16px 16px;\n      }\n\n      &.is-autocheck-loading {\n        background-image: url(\"/images/spinners/octocat-spinner-32.gif\");\n      }\n\n      &.is-autocheck-successful {\n        background-image: url(\"/images/modules/ajax/success@2x.png\");\n      }\n\n      &.is-autocheck-errored {\n        background-image: url(\"/images/modules/ajax/error@2x.png\");\n      }\n    }\n  }\n}\n\n// Inline verification\n// This overrides primer's inline form stuff\n.status-indicator {\n  display: inline-block;\n  width: 16px;\n  height: 16px;\n  margin-left: 5px;\n\n  .octicon {\n    display: none;\n  }\n}\n\n.status-indicator-success {\n  // Override primer\n  &::before {\n    content: \"\";\n  }\n\n  .octicon-check {\n    display: inline-block;\n    color: $green;\n    fill: $green;\n  }\n\n  .octicon-x {\n    display: none;\n  }\n}\n\n.status-indicator-failed {\n  // Override primer\n  &::before {\n    content: \"\";\n  }\n\n  .octicon-check {\n    display: none;\n  }\n\n  .octicon-x {\n    display: inline-block;\n    color: $text-red;\n    fill: $red;\n  }\n}\n\n.status-indicator-loading {\n  width: 16px;\n  background: url(\"/images/spinners/octocat-spinner-32-EAF2F5.gif\") 0 0 no-repeat;\n  background-size: 16px;\n}\n\n.inline-form {\n  display: inline-block;\n\n  .btn-plain {\n    background-color: transparent;\n    border: 0;\n  }\n}\n\n// Drag and drop\n//\n// Previously part of `_forms.scss` in Primer. Needs accounting for.\n\n.drag-and-drop {\n  padding: 7px 10px;\n  margin: 0;\n  font-size: 13px;\n  line-height: 16px;\n  color: $text-gray;\n  background-color: $gray-000;\n  border: 1px solid darken($gray-300, 5%);\n  border-top: 0;\n  border-bottom-right-radius: 3px;\n  border-bottom-left-radius: 3px;\n\n  .default,\n  .loading,\n  .error {\n    display: none;\n  }\n\n  .error {\n    color: $text-red;\n  }\n\n  // Spinner\n  img {\n    vertical-align: top;\n  }\n}\n\n.is-default .drag-and-drop .default {\n  display: inline-block;\n}\n\n.is-uploading .drag-and-drop .loading {\n  display: inline-block;\n}\n\n.is-bad-file .drag-and-drop .bad-file {\n  display: inline-block;\n}\n\n.is-duplicate-filename .drag-and-drop .duplicate-filename {\n  display: inline-block;\n}\n\n.is-too-big .drag-and-drop .too-big {\n  display: inline-block;\n}\n\n.is-hidden-file .drag-and-drop .hidden-file {\n  display: inline-block;\n}\n\n.is-empty .drag-and-drop .empty {\n  display: inline-block;\n}\n\n.is-bad-permissions .drag-and-drop .bad-permissions {\n  display: inline-block;\n}\n\n.is-repository-required .drag-and-drop .repository-required {\n  display: inline-block;\n}\n\n.drag-and-drop-error-info {\n  font-weight: $font-weight-normal;\n  color: $text-gray;\n\n  a {\n    color: $text-blue;\n  }\n}\n\n.is-failed .drag-and-drop .failed-request {\n  display: inline-block;\n}\n\n.manual-file-chooser {\n  position: absolute;\n  width: 240px;\n  padding: 5px;\n  margin-left: -80px;\n  cursor: pointer;\n  opacity: 0.0001;\n}\n\n.manual-file-chooser:hover + .manual-file-chooser-text {\n  text-decoration: underline;\n}\n\n.btn {\n  // align manual-file-chooser inside a button\n  .manual-file-chooser {\n    top: 0;\n    padding: 0;\n    line-height: 34px;\n  }\n}\n\n// Focused Textarea styles\n.upload-enabled {\n  textarea {\n    display: block;\n    border-bottom: 1px dashed lighten($gray-300, 5%);\n    border-bottom-right-radius: 0;\n    border-bottom-left-radius: 0;\n  }\n\n  &.focused {\n    border-radius: 3px;\n    box-shadow: $form-control-shadow, $btn-input-focus-shadow;\n\n    .form-control {\n      box-shadow: none;\n    }\n\n    .drag-and-drop {\n      border-color: lighten($blue-400, 8%);\n    }\n  }\n}\n\n// Dropping a file on top\n.dragover textarea,\n.dragover .drag-and-drop {\n  box-shadow: rgba(#c9ff00, 1) 0 0 3px;\n}\n\n.write-content {\n  position: relative;\n}\n\n// Form style with a write and a preview tab\n.previewable-comment-form {\n  position: relative;\n\n  .tabnav {\n    position: relative;\n    padding: $spacer-2 $spacer-2 0;\n  }\n\n  .comment {\n    border: 1px solid darken($gray-300, 5%);\n  }\n\n  .comment-form-error { margin-bottom: $spacer-2; }\n\n  .write-content,\n  .preview-content {\n    display: none;\n    margin: 0 $spacer-2 $spacer-2;\n  }\n\n  &.write-selected .write-content,\n  &.preview-selected .preview-content {\n    display: block;\n  }\n\n  textarea {\n    display: block;\n    width: 100%;\n    min-height: 100px;\n    max-height: 500px;\n    padding: $spacer-2;\n    resize: vertical;\n  }\n}\n\n// Used in our boxed-group-less form styles. Give the sumbit button enough space\n// to breathe without the need for the extra hr.\n.form-action-spacious {\n  margin-top: 10px;\n}\n\n// A two column form, with a .main and a .sidebar column\n//\n// Override some `.timeline-comment-wrapper` defaults.\n// The `div` is needed to be more specific than the other class.\ndiv.composer {\n  margin-top: 0;\n  border: 0;\n}\n\n// Override the previewable comment form defaults\n.composer .comment-form-textarea {\n  height: 200px;\n  min-height: 200px;\n}\n\n.composer .tabnav { margin: 0 0 10px; }\n\n// Misc CSS\n//\n// Previously part of `_forms.scss` in Primer. Needs accounting for.\n\nh2.account {\n  margin: 15px 0 0;\n  font-size: 18px;\n  font-weight: $font-weight-normal;\n  color: $gray-600;\n}\n\np.explain {\n  position: relative;\n  font-size: $font-size-small;\n  color: $gray-600;\n\n  strong {\n    color: $text-gray-dark;\n  }\n\n  .octicon {\n    margin-right: 5px;\n    color: $gray-400;\n  }\n\n  .minibutton {\n    top: -4px;\n    float: right;\n  }\n}\n\n// fix for chrome bug, see https://github.com/github/github/issues/53931\n.form-group label {\n  position: static;\n}\n",".input-group {\n  display: table;\n\n  .form-control {\n    position: relative;\n    width: 100%;\n\n    &:focus {\n      z-index: 2;\n    }\n\n    + .btn {\n      margin-left: 0;\n    }\n  }\n\n  // For when you want the input group to behave like inline-block.\n  &.inline {\n    display: inline-table;\n  }\n}\n\n.input-group .form-control,\n.input-group-button {\n  display: table-cell;\n}\n\n.input-group-button {\n  width: 1%;\n  vertical-align: middle; // Match the inputs\n}\n\n.input-group .form-control:first-child,\n.input-group-button:first-child .btn {\n  border-top-right-radius: 0;\n  border-bottom-right-radius: 0;\n}\n\n.input-group-button:first-child .btn {\n  margin-right: -1px;\n}\n\n.input-group .form-control:last-child,\n.input-group-button:last-child .btn {\n  border-top-left-radius: 0;\n  border-bottom-left-radius: 0;\n}\n\n.input-group-button:last-child .btn {\n  margin-left: -1px;\n}\n","// Fixed-width, centered column for site content.\n// This will be deprecated and replaced with container-lg in future\n.container {\n  width: $container-width;\n  margin-right: auto;\n  margin-left: auto;\n  @include clearfix;\n}\n\n// Handy container styles that match our breakpoints\n\n// 544px\n.container-sm {\n  max-width: $width-sm;\n  margin-right: auto;\n  margin-left: auto;\n}\n\n// 768px\n.container-md {\n  max-width: $container-md;\n  margin-right: auto;\n  margin-left: auto;\n}\n\n// 1004px - this matches the current fixed width: 980px + padding: px-3\n.container-lg {\n  max-width: $container-lg;\n  margin-right: auto;\n  margin-left: auto;\n}\n\n// 1280px\n.container-xl {\n  max-width: $container-xl;\n  margin-right: auto;\n  margin-left: auto;\n}\n","// Grid system\n//\n// Create rows with `.columns` to clear the floated columns and outdent the\n// padding on `.column`s with negative margin for alignment.\n\n@warn \".columns, .column, .one-third, .two-thirds, .one-fourth, .one-half, .three-fourths, .one-fifth, .four-fifths, .centered will be deprecated in 12.0.0 please migrate to grid.scss\";\n\n.columns {\n  margin-right: -$grid-gutter;\n  margin-left: -$grid-gutter;\n  @include clearfix;\n}\n\n// Base class for every column (requires a column width from below)\n.column {\n  float: left;\n  padding-right: $grid-gutter;\n  padding-left: $grid-gutter;\n}\n\n// Column widths\n.one-third {\n  width: 33.333333%;\n}\n\n.two-thirds {\n  width: 66.666667%;\n}\n\n.one-fourth {\n  width: 25%;\n}\n\n.one-half {\n  width: 50%;\n}\n\n.three-fourths {\n  width: 75%;\n}\n\n.one-fifth {\n  width: 20%;\n}\n\n.four-fifths {\n  width: 80%;\n}\n\n// Centers content horizontally. Can be used inside or outside the grid.\n.centered {\n  display: block;\n  float: none;\n  margin-right: auto;\n  margin-left: auto;\n}\n","// GRID\n\n// Columns\n.col-1 { width: (1 / 12 * 100%); }\n.col-2 { width: (2 / 12 * 100%); }\n.col-3 { width: (3 / 12 * 100%); }\n.col-4 { width: (4 / 12 * 100%); }\n.col-5 { width: (5 / 12 * 100%); }\n.col-6 { width: (6 / 12 * 100%); }\n.col-7 { width: (7 / 12 * 100%); }\n.col-8 { width: (8 / 12 * 100%); }\n.col-9 { width: (9 / 12 * 100%); }\n.col-10 { width: (10 / 12 * 100%); }\n.col-11 { width: (11 / 12 * 100%); }\n.col-12 { width: 100%; }\n\n@each $breakpoint in map-keys($breakpoints) {\n  @include breakpoint($breakpoint) {\n    .col-#{$breakpoint}-1 { width: ( 1 / 12 * 100%); }\n    .col-#{$breakpoint}-2 { width: ( 2 / 12 * 100%); }\n    .col-#{$breakpoint}-3 { width: ( 3 / 12 * 100%); }\n    .col-#{$breakpoint}-4 { width: ( 4 / 12 * 100%); }\n    .col-#{$breakpoint}-5 { width: ( 5 / 12 * 100%); }\n    .col-#{$breakpoint}-6 { width: ( 6 / 12 * 100%); }\n    .col-#{$breakpoint}-7 { width: ( 7 / 12 * 100%); }\n    .col-#{$breakpoint}-8 { width: ( 8 / 12 * 100%); }\n    .col-#{$breakpoint}-9 { width: ( 9 / 12 * 100%); }\n    .col-#{$breakpoint}-10 { width: ( 10 / 12 * 100%); }\n    .col-#{$breakpoint}-11 { width: ( 11 / 12 * 100%); }\n    .col-#{$breakpoint}-12 { width: 100%; }\n  }\n}\n\n// Gutters\n// Apply padding and a negative margin to the outside of the container\n@mixin gutters ($gutter-width: $spacer-3) {\n  margin-right: -$gutter-width;\n  margin-left: -$gutter-width;\n\n  > [class*=\"col-\"] {\n    padding-right: $gutter-width !important;\n    padding-left: $gutter-width !important;\n  }\n}\n\n.gutter {\n  @include gutters($spacer-3);\n}\n\n.gutter-condensed {\n  @include gutters($spacer-2);\n}\n\n.gutter-spacious {\n  @include gutters($spacer-4);\n}\n\n@each $breakpoint in map-keys($breakpoints) {\n  @include breakpoint($breakpoint) {\n    .gutter-#{$breakpoint} { @include gutters($spacer-3); }\n    .gutter-#{$breakpoint}-condensed { @include gutters($spacer-2); }\n    .gutter-#{$breakpoint}-spacious { @include gutters($spacer-4); }\n  }\n}\n","// Optional offset options to work with grid.scss\n\n// Offset Columns\n\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    .offset#{$variant}-1 { margin-left: (1 / 12 * 100%) !important; }\n    .offset#{$variant}-2 { margin-left: (2 / 12 * 100%) !important; }\n    .offset#{$variant}-3 { margin-left: (3 / 12 * 100%) !important; }\n    .offset#{$variant}-4 { margin-left: (4 / 12 * 100%) !important; }\n    .offset#{$variant}-5 { margin-left: (5 / 12 * 100%) !important; }\n    .offset#{$variant}-6 { margin-left: (6 / 12 * 100%) !important; }\n    .offset#{$variant}-7 { margin-left: (7 / 12 * 100%) !important; }\n    .offset#{$variant}-8 { margin-left: (8 / 12 * 100%) !important; }\n    .offset#{$variant}-9 { margin-left: (9 / 12 * 100%) !important; }\n    .offset#{$variant}-10 { margin-left: (10 / 12 * 100%) !important; }\n    .offset#{$variant}-11 { margin-left: (11 / 12 * 100%) !important; }\n  }\n}\n","// Side menu\n//\n// A menu on the side of a page, defaults to left side. e.g. github.com/about\n\n.menu {\n  margin-bottom: 15px;\n  list-style: none;\n  background-color: $bg-white;\n  border: 1px solid $gray-300;\n  border-radius: 3px;\n}\n\n.menu-item {\n  position: relative;\n  display: block;\n  padding: $spacer-2 10px;\n  border-bottom: 1px solid $gray-200;\n\n  &:first-child {\n    border-top: 0;\n    border-top-left-radius: 2px;\n    border-top-right-radius: 2px;\n\n    &::before { border-top-left-radius: 2px; }\n  }\n\n  &:last-child {\n    border-bottom: 0;\n    border-bottom-right-radius: 2px;\n    border-bottom-left-radius: 2px;\n\n    &::before { border-bottom-left-radius: 2px; }\n  }\n\n  &:hover {\n    text-decoration: none;\n    background-color: $gray-100;\n  }\n\n  &.selected {\n    font-weight: $font-weight-bold;\n    color: $text-gray-dark;\n    cursor: default;\n    background-color: $bg-white;\n\n    &::before {\n      position: absolute;\n      top: 0;\n      bottom: 0;\n      left: 0;\n      width: 2px;\n      content: \"\";\n      background-color: $orange-600;\n    }\n  }\n\n  .octicon {\n    width: 16px;\n    margin-right: 5px;\n    color: $text-gray-dark;\n    text-align: center;\n  }\n\n  .Counter {\n    float: right;\n    margin-left: 5px;\n  }\n\n  .menu-warning {\n    float: right;\n    color: $red-900;\n  }\n\n  .avatar {\n    float: left;\n    margin-right: 5px;\n  }\n\n  &.alert {\n    .Counter {\n      color: $text-red;\n    }\n  }\n}\n\n.menu-heading {\n  display: block;\n  padding: $spacer-2 10px;\n  margin-top: 0;\n  margin-bottom: 0;\n  font-size: 13px;\n  font-weight: $font-weight-bold;\n  line-height: 20px;\n  color: $gray-600;\n  background-color: darken($gray-100, 1%);\n  border-bottom: 1px solid $gray-200;\n\n  &:hover {\n    text-decoration: none;\n  }\n\n  &:first-child {\n    border-top-left-radius: 2px;\n    border-top-right-radius: 2px;\n  }\n\n  &:last-child {\n    border-bottom: 0;\n    border-bottom-right-radius: 2px;\n    border-bottom-left-radius: 2px;\n  }\n}\n","// Outer wrapper\n// stylelint-disable selector-max-type\n.tabnav {\n  margin-top: 0;\n  margin-bottom: 15px;\n  border-bottom: 1px solid $gray-300;\n\n  .Counter {\n    margin-left: 5px;\n  }\n}\n\n.tabnav-tabs {\n  margin-bottom: -1px;\n}\n\n.tabnav-tab {\n  display: inline-block;\n  padding: $spacer-2 12px;\n  font-size: $h5-size;\n  line-height: 20px;\n  color: $gray-600;\n  text-decoration: none;\n  background-color: transparent;\n  border: 1px solid transparent;\n  border-bottom: 0;\n\n  &[aria-selected=true],\n  &.selected {\n    color: $text-gray-dark;\n    background-color: $bg-white;\n    border-color: $gray-300;\n    border-radius: 3px 3px 0 0;\n  }\n\n  &:hover,\n  &:focus {\n    color: $text-gray-dark;\n    text-decoration: none;\n  }\n}\n\n// Tabnav extras\n//\n// Tabnav extras are non-tab elements that sit in the tabnav. Usually they're\n// inline text or links.\n\n.tabnav-extra {\n  display: inline-block;\n  padding-top: 10px;\n  margin-left: 10px;\n  font-size: $font-size-small;\n  color: $gray-600;\n\n  > .octicon {\n    margin-right: 2px;\n  }\n}\n\n// When tabnav-extra are anchors\n// stylelint-disable selector-no-qualifying-type\na.tabnav-extra:hover {\n  color: $text-blue;\n  text-decoration: none;\n}\n// stylelint-enable selector-no-qualifying-type\n\n// Tabnav buttons\n//\n// For when there are multiple buttons, space them out appropriately. Requires\n// the buttons to be floated or inline-block.\n\n.tabnav-btn {\n  margin-left: 10px;\n}\n","// Filters list\n//\n// A vertical list of filters.\n.filter-list {\n  list-style-type: none;\n\n  &.small .filter-item {\n    padding: $spacer-1 10px;\n    margin: 0 0 2px;\n    font-size: $font-size-small;\n  }\n\n  &.pjax-active .filter-item {\n    color: $text-gray;\n    background-color: transparent;\n\n    &.pjax-active {\n      color: $text-white;\n      background-color: $bg-blue;\n    }\n  }\n}\n\n.filter-item {\n  position: relative;\n  display: block;\n  padding: $spacer-2 10px;\n  margin-bottom: 5px;\n  overflow: hidden;\n  font-size: $h5-size;\n  color: $text-gray;\n  text-decoration: none;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  cursor: pointer;\n  border-radius: 3px;\n\n  &:hover {\n    text-decoration: none;\n    background-color: lighten($gray-200, 3%);\n  }\n\n  &.selected {\n    color: $text-white;\n    background-color: $bg-blue;\n  }\n\n  .count {\n    float: right;\n    font-weight: $font-weight-bold;\n  }\n\n  .bar {\n    position: absolute;\n    top: 2px;\n    right: 0;\n    bottom: 2px;\n    z-index: -1;\n    display: inline-block;\n    background-color: darken($gray-100, 2%);\n  }\n}\n","// Needs refactoring\n// Sub nav\n.subnav {\n  margin-bottom: 20px;\n\n  @include clearfix();\n}\n\n.subnav-bordered {\n  padding-bottom: 20px;\n  border-bottom: 1px solid lighten($gray-200, 3%);\n}\n\n.subnav-flush {\n  margin-bottom: 0;\n}\n\n.subnav-item {\n  position: relative;\n  float: left;\n  padding: 6px 14px;\n  font-weight: $font-weight-bold;\n  line-height: 20px;\n  color: $gray-600;\n  border: 1px solid $gray-200;\n\n  + .subnav-item {\n    margin-left: -1px;\n  }\n\n  &:hover,\n  &:focus {\n    text-decoration: none;\n    background-color: $gray-100;\n  }\n\n  &.selected,\n  &.selected:hover,\n  &.selected:focus {\n    z-index: 2;\n    color: $text-white;\n    background-color: $bg-blue;\n    border-color: $blue;\n  }\n\n  &:first-child {\n    border-top-left-radius: 3px;\n    border-bottom-left-radius: 3px;\n  }\n\n  &:last-child {\n    border-top-right-radius: 3px;\n    border-bottom-right-radius: 3px;\n  }\n}\n\n.subnav-search {\n  position: relative;\n  margin-left: 10px;\n}\n\n.subnav-search-input {\n  width: 320px;\n  padding-left: 30px;\n  color: $text-gray;\n}\n\n.subnav-search-input-wide {\n  width: 500px;\n}\n\n.subnav-search-icon {\n  position: absolute;\n  top: 9px;\n  left: 8px;\n  display: block;\n  color: darken($gray-300, 4%);\n  text-align: center;\n  pointer-events: none;\n}\n\n.subnav-search-context {\n  .btn {\n    color: $gray-700;\n    border-top-right-radius: 0;\n    border-bottom-right-radius: 0;\n\n    &:hover,\n    &:focus,\n    &:active,\n    &.selected {\n      z-index: 2;\n    }\n  }\n\n  + .subnav-search {\n    margin-left: -1px;\n\n    .subnav-search-input {\n      border-top-left-radius: 0;\n      border-bottom-left-radius: 0;\n    }\n  }\n\n  .select-menu-modal-holder {\n    z-index: 30;\n  }\n\n  .select-menu-modal {\n    width: 220px;\n  }\n\n  .select-menu-item-icon {\n    color: inherit;\n  }\n}\n\n.subnav-spacer-right {\n  padding-right: 10px;\n}\n",