{\n      border-top-right-radius: $border-radius;\n      border-bottom-right-radius: $border-radius;\n    }\n\n    // Bring any button into forefront for proper borders given negative margin below\n    &:hover,\n    &:focus {\n      z-index: 2;\n      text-decoration: none;\n      background-color: darken($gray-100, 2%);\n      border-color: $border-gray;\n    }\n  }\n\n  .selected { z-index: 3; }\n\n  .current,\n  .current:hover {\n    z-index: 3;\n    color: $text-white;\n    background-color: $bg-blue;\n    border-color: $border-blue;\n  }\n\n  .gap,\n  .disabled,\n  .gap:hover,\n  .disabled:hover {\n    color: $gray-300;\n    cursor: default;\n    background-color: $bg-gray-light;\n  }\n}\n\n// Unified centered pagination across the site\n.paginate-container {\n  margin-top: 20px;\n  margin-bottom: 15px;\n  text-align: center;\n\n  .pagination {\n    display: inline-block;\n  }\n}\n",".tooltipped {\n  position: relative;\n}\n\n// This is the tooltip bubble\n.tooltipped::after {\n  position: absolute;\n  z-index: 1000000;\n  display: none;\n  padding: $em-spacer-5 $em-spacer-6;\n  font: normal normal 11px/1.5 $body-font;\n  -webkit-font-smoothing: subpixel-antialiased;\n  color: $tooltip-text-color;\n  text-align: center;\n  text-decoration: none;\n  text-shadow: none;\n  text-transform: none;\n  letter-spacing: normal;\n  word-wrap: break-word;\n  white-space: pre;\n  pointer-events: none;\n  content: attr(aria-label);\n  background: $tooltip-background-color;\n  border-radius: $border-radius;\n  opacity: 0;\n}\n\n// This is the tooltip arrow\n.tooltipped::before {\n  position: absolute;\n  z-index: 1000001;\n  display: none;\n  width: 0;\n  height: 0;\n  color: $tooltip-background-color;\n  pointer-events: none;\n  content: \"\";\n  border: 6px $border-style transparent;\n  opacity: 0;\n}\n\n// delay animation for tooltip\n@keyframes tooltip-appear {\n  from {\n    opacity: 0;\n  }\n\n  to {\n    opacity: 1;\n  }\n}\n\n// This will indicate when we'll activate the tooltip\n.tooltipped:hover,\n.tooltipped:active,\n.tooltipped:focus {\n  &::before,\n  &::after {\n    display: inline-block;\n    text-decoration: none;\n    animation-name: tooltip-appear;\n    animation-duration: $tooltip-duration;\n    animation-fill-mode: forwards;\n    animation-timing-function: ease-in;\n    animation-delay: $tooltip-delay;\n  }\n}\n\n.tooltipped-no-delay:hover,\n.tooltipped-no-delay:active,\n.tooltipped-no-delay:focus {\n  &::before,\n  &::after {\n    animation-delay: 0s;\n  }\n}\n\n.tooltipped-multiline:hover,\n.tooltipped-multiline:active,\n.tooltipped-multiline:focus {\n  &::after {\n    display: table-cell;\n  }\n}\n\n// Tooltipped south\n.tooltipped-s,\n.tooltipped-se,\n.tooltipped-sw {\n  &::after {\n    top: 100%;\n    right: 50%;\n    margin-top: 6px;\n  }\n\n  &::before {\n    top: auto;\n    right: 50%;\n    bottom: -7px;\n    margin-right: -6px;\n    border-bottom-color: $tooltip-background-color;\n  }\n}\n\n.tooltipped-se {\n  &::after {\n    right: auto;\n    left: 50%;\n    margin-left: -$spacer-3;\n  }\n}\n\n.tooltipped-sw::after {\n  margin-right: -$spacer-3;\n}\n\n// Tooltips above the object\n.tooltipped-n,\n.tooltipped-ne,\n.tooltipped-nw {\n  &::after {\n    right: 50%;\n    bottom: 100%;\n    margin-bottom: 6px;\n  }\n\n  &::before {\n    top: -7px;\n    right: 50%;\n    bottom: auto;\n    margin-right: -6px;\n    border-top-color: $tooltip-background-color;\n  }\n}\n\n.tooltipped-ne {\n  &::after {\n    right: auto;\n    left: 50%;\n    margin-left: -$spacer-3;\n  }\n}\n\n.tooltipped-nw::after {\n  margin-right: -$spacer-3;\n}\n\n// Move the tooltip body to the center of the object.\n.tooltipped-s::after,\n.tooltipped-n::after {\n  transform: translateX(50%);\n}\n\n// Tooltipped to the left\n.tooltipped-w {\n  &::after {\n    right: 100%;\n    bottom: 50%;\n    margin-right: 6px;\n    transform: translateY(50%);\n  }\n\n  &::before {\n    top: 50%;\n    bottom: 50%;\n    left: -7px;\n    margin-top: -6px;\n    border-left-color: $tooltip-background-color;\n  }\n}\n\n// tooltipped to the right\n.tooltipped-e {\n  &::after {\n    bottom: 50%;\n    left: 100%;\n    margin-left: 6px;\n    transform: translateY(50%);\n  }\n\n  &::before {\n    top: 50%;\n    right: -7px;\n    bottom: 50%;\n    margin-top: -6px;\n    border-right-color: $tooltip-background-color;\n  }\n}\n\n// Tooltip align right and left\n.tooltipped-align-right-1,\n.tooltipped-align-right-2 {\n  &::after {\n    right: 0;\n    margin-right: 0;\n  }\n}\n\n.tooltipped-align-right-1 {\n  &::before {\n    right: 10px;\n  }\n}\n\n.tooltipped-align-right-2 {\n  &::before {\n    right: 15px;\n  }\n}\n\n.tooltipped-align-left-1,\n.tooltipped-align-left-2, {\n  &::after {\n    left: 0;\n    margin-left: 0;\n  }\n}\n\n.tooltipped-align-left-1 {\n  &::before {\n    left: 5px;\n  }\n}\n\n.tooltipped-align-left-2 {\n  &::before {\n    left: 10px;\n  }\n}\n\n// Multiline tooltips\n//\n// `.tooltipped-multiline` Add this class when you have long content.\n// The downside is you cannot preformat the text with newlines and `[w,e]`\n// are always `$tooltip-max-width` wide.\n.tooltipped-multiline {\n  &::after {\n    width: max-content;\n    max-width: $tooltip-max-width;\n    word-wrap: break-word;\n    white-space: pre-line;\n    border-collapse: separate;\n  }\n\n  &.tooltipped-s::after,\n  &.tooltipped-n::after {\n    right: auto;\n    left: 50%;\n    transform: translateX(-50%);\n  }\n\n  &.tooltipped-w::after,\n  &.tooltipped-e::after {\n    right: 100%;\n  }\n}\n\n@media screen and (min-width: 0\\0) {\n  // IE11\n  .tooltipped-multiline::after {\n    width: $tooltip-max-width;\n  }\n}\n\n// Sticky tooltips\n//\n// Always show the tooltip.\n.tooltipped-sticky {\n  &::before,\n  &::after {\n    display: inline-block;\n  }\n\n  &.tooltipped-multiline {\n    &::after {\n      display: table-cell;\n    }\n  }\n}\n","// Truncate\n//\n// css-truncate will shorten text with an ellipsis.\n\n.css-truncate {\n  // Truncate double target