.flex#{$variant}-content-between  { align-content: space-between !important; }\n    .flex#{$variant}-content-around   { align-content: space-around !important; }\n    .flex#{$variant}-content-stretch  { align-content: stretch !important; }\n\n    // Item\n    .flex#{$variant}-auto { flex: 1 1 auto !important; }\n    .flex#{$variant}-shrink-0 { flex-shrink: 0 !important; }\n\n    .flex#{$variant}-self-auto        { align-self: auto !important; }\n    .flex#{$variant}-self-start       { align-self: flex-start !important; }\n    .flex#{$variant}-self-end         { align-self: flex-end !important; }\n    .flex#{$variant}-self-center      { align-self: center !important; }\n    .flex#{$variant}-self-baseline    { align-self: baseline !important; }\n    .flex#{$variant}-self-stretch     { align-self: stretch !important; }\n\n    // Shorthand for equal width and height cols\n    .flex#{$variant}-item-equal {\n      flex-grow: 1;\n      flex-basis: 0;\n    }\n  }\n}\n","// Layout\n// stylelint-disable primer/selector-no-utility\n// stylelint-disable block-opening-brace-space-after, block-opening-brace-space-before, comment-empty-line-before\n\n// Loop through the breakpoint values\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    @each $position in $responsive-positions {\n      .position#{$variant}-#{$position} {\n        position: $position !important;\n      }\n    }\n  }\n}\n\n/* Set top 0 */\n.top-0    { top: 0 !important; }\n/* Set right 0 */\n.right-0  { right: 0 !important; }\n/* Set bottom 0 */\n.bottom-0 { bottom: 0 !important; }\n/* Set left 0 */\n.left-0   { left: 0 !important; }\n\n/* Vertical align middle */\n.v-align-middle      { vertical-align: middle !important; }\n/* Vertical align top */\n.v-align-top         { vertical-align: top !important; }\n/* Vertical align bottom */\n.v-align-bottom      { vertical-align: bottom !important; }\n/* Vertical align to the top of the text */\n.v-align-text-top    { vertical-align: text-top !important; }\n/* Vertical align to the bottom of the text */\n.v-align-text-bottom { vertical-align: text-bottom !important; }\n/* Vertical align to the parent's baseline */\n.v-align-baseline    { vertical-align: baseline !important; }\n\n// Overflow utilities\n@each $overflow in (visible, hidden, auto, scroll) {\n  .overflow-#{$overflow} { overflow: $overflow !important; }\n  .overflow-x-#{$overflow} { overflow-x: $overflow !important; }\n  .overflow-y-#{$overflow} { overflow-y: $overflow !important; }\n}\n\n// Clear floats\n/* Clear floats around the element */\n.clearfix {\n  @include clearfix;\n}\n\n// Floats\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    /* Float to the left */\n    .float#{$variant}-left  { float: left !important; }\n    /* Float to the right */\n    .float#{$variant}-right  { float: right !important; }\n    /* No float */\n    .float#{$variant}-none { float: none !important; }\n  }\n}\n\n// Width and height utilities, helpful in combination\n// with display-table utilities and images\n/* Max width 100% */\n.width-fit   { max-width: 100% !important; }\n/* Set the width to 100% */\n.width-full  { width: 100% !important; }\n/* Max height 100% */\n.height-fit  { max-height: 100% !important; }\n/* Set the height to 100% */\n.height-full { height: 100% !important; }\n\n/* Remove min-width from element */\n.min-width-0 { min-width: 0 !important; }\n\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    /* Set the direction to rtl */\n    .direction#{$variant}-rtl { direction: rtl !important; }\n    /* Set the direction to ltr */\n    .direction#{$variant}-ltr { direction: ltr !important; }\n  }\n}\n","// Margin spacer utilities\n// stylelint-disable primer/selector-no-utility\n// stylelint-disable block-opening-brace-space-before, declaration-colon-space-before, comment-empty-line-before\n\n// Loop through the breakpoint values\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    // Loop through the spacer values\n    @each $scale, $size in $spacer-map {\n      /* Set a $size margin to all sides at $breakpoint */\n      .m#{$variant}-#{$scale}  { margin: $size !important; }\n      /* Set a $size margin on the top at $breakpoint */\n      .mt#{$variant}-#{$scale} { margin-top: $size !important; }\n      /* Set a $size margin on the right at $breakpoint */\n      .mr#{$variant}-#{$scale} { margin-right: $size !important; }\n      /* Set a $size margin on the bottom at $breakpoint */\n      .mb#{$variant}-#{$scale} { margin-bottom: $size !important; }\n      /* Set a $size margin on the left at $breakpoint */\n      .ml#{$variant}-#{$scale} { margin-left: $size !important; }\n\n      @if ($size != 0) {\n        /* Set a negative $size margin on top at $breakpoint */\n        .mt#{$variant}-n#{$scale} { margin-top   : -$size !important; }\n        /* Set a negative $size margin on the right at $breakpoint */\n        .mr#{$variant}-n#{$scale} { margin-right : -$size !important; }\n        /* Set a negative $size margin on the bottom at $breakpoint */\n        .mb#{$variant}-n#{$scale} { margin-bottom: -$size !important; }\n        /* Set a negative $size margin on the left at $breakpoint */\n        .ml#{$variant}-n#{$scale} { margin-left  : -$size !important; }\n      }\n\n      /* Set a $size margin on the left & right at $breakpoint */\n      .mx#{$variant}-#{$scale} {\n        margin-right: $size !important;\n        margin-left: $size !important;\n      }\n\n      /* Set a $size margin on the top & bottom at $breakpoint */\n      .my#{$variant}-#{$scale} {\n        margin-top: $size !important;\n        margin-bottom: $size !important;\n      }\n    }\n\n    /* responsive horizontal auto margins */\n    .mx#{$variant}-auto {\n      margin-right: auto !important;\n      margin-left: auto !important;\n    }\n  }\n}\n","// Padding spacer utilities\n// stylelint-disable primer/selector-no-utility\n// stylelint-disable block-opening-brace-space-before, declaration-colon-space-before, comment-empty-line-before\n\n// Responsive padding spacer utilities\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    // Loop through the spacer values\n    @each $scale, $size in $spacer-map {\n      /* Set a $size padding to all sides at $breakpoint */\n      .p#{$variant}-#{$scale}  { padding: $size !important; }\n      /* Set a $size padding to the top at $breakpoint */\n      .pt#{$variant}-#{$scale} { padding-top: $size !important; }\n      /* Set a $size padding to the right at $breakpoint */\n      .pr#{$variant}-#{$scale} { padding-right: $size !important; }\n      /* Set a $size padding to the bottom at $breakpoint */\n      .pb#{$variant}-#{$scale} { padding-bottom: $size !important; }\n      /* Set a $size padding to the left at $breakpoint */\n      .pl#{$variant}-#{$scale} { padding-left: $size !important; }\n\n      /* Set a $size padding to the left & right at $breakpoint */\n      .px#{$variant}-#{$scale} {\n        padding-right: $size !important;\n        padding-left: $size !important;\n      }\n\n      /* Set a $size padding to the top & bottom at $breakpoint */\n      .py#{$variant}-#{$scale} {\n        padding-top: $size !important;\n        padding-bottom: $size !important;\n      }\n    }\n  }\n}\n\n// responsive padding for containers\n.p-responsive {\n  padding-right: $spacer-3 !important;\n  padding-left: $spacer-3 !important;\n\n  @include breakpoint(sm) {\n    padding-right: $spacer-6 !important;\n    padding-left: $spacer-6 !important;\n  }\n\n  @include breakpoint(lg) {\n    padding-right: $spacer-3 !important;\n    padding-left: $spacer-3 !important;\n  }\n}\n","// stylelint-disable primer/selector-no-utility\n// stylelint-disable block-closing-brace-space-before, selector-list-comma-newline-after, comment-empty-line-before\n\n// Type scale variables found in ../support/lib/variables.scss\n// $h00-size-mobile: 40px;\n// $h0-size-mobile: 32px;\n// $h1-size-mobile: 26px;\n// $h2-size-mobile: 22px;\n// $h3-size-mobile: 18px;\n// $h00-size: 48px;\n// $h0-size: 40px;\n// $h1-size: 32px;\n// $h2-size: 24px;\n// $h3-size: 20px;\n// $h4-size: 16px;\n// $h5-size: 14px;\n// $h6-size: 12px;\n\n/* Set the font size to 26px */\n.h1 {\n  font-size: $h1-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h1-size !important; }\n}\n\n/* Set the font size to 22px */\n.h2 {\n  font-size: $h2-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h2-size !important; }\n}\n\n/* Set the font size to 18px */\n.h3 {\n  font-size: $h3-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h3-size !important; }\n}\n\n/* Set the font size to #{$h4-size} */\n.h4 {\n  font-size: $h4-size !important;\n}\n\n/* Set the font size to #{$h5-size} */\n.h5 { font-size: $h5-size !important; }\n\n// Does not include color property like typography base\n// styles, color should be applied with color utilities.\n/* Set the font size to #{$h6-size} */\n.h6 { font-size: $h6-size !important; }\n\n// Heading utilities\n.h1, .h2, .h3, .h4, .h5, .h6 { font-weight: $font-weight-bold !important; }\n\n// Type utilities that match type sale\n/* Set the font size to 26px */\n.f1 {\n  font-size: $h1-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h1-size !important; }\n}\n\n/* Set the font size to 22px */\n.f2 {\n  font-size: $h2-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h2-size !important; }\n}\n\n/* Set the font size to 18px */\n.f3 {\n  font-size: $h3-size-mobile !important;\n\n  @include breakpoint(md) { font-size: $h3-size !important; }\n}\n\n/* Set the font size to #{$h4-size} */\n.f4 {\n  font-size: $h4-size !important;\n\n  @include breakpoint(md) { font-size: $h4-size !important; }\n}\n\n/* Set the font size to #{$h5-size} */\n.f5 { font-size: $h5-size !important; }\n/* Set the font size to #{$h6-size} */\n.f6 { font-size: $h6-size !important; }\n\n// Type utils with light weight that match type scale\n/* Set the font size to 40px and weight to light */\n.f00-light {\n  font-size: $h00-size-mobile !important;\n  font-weight: $font-weight-light !important;\n\n  @include breakpoint(md) { font-size: $h00-size !important; }\n}\n\n/* Set the font size to 32px and weight to light */\n.f0-light {\n  font-size: $h0-size-mobile !important;\n  font-weight: $font-weight-light !important;\n\n  @include breakpoint(md) { font-size: $h0-size !important; }\n}\n\n/* Set the font size to 26px and weight to light */\n.f1-light {\n  font-size: $h1-size-mobile !important;\n  font-weight: $font-weight-light !important;\n\n  @include breakpoint(md) { font-size: $h1-size !important; }\n}\n\n/* Set the font size to 22px and weight to light */\n.f2-light {\n  font-size: $h2-size-mobile !important;\n  font-weight: $font-weight-light !important;\n\n  @include breakpoint(md) { font-size: $h2-size !important; }\n}\n\n// Same size and weight as .lead but without color property\n/* Set the font size to 18px and weight to light */\n.f3-light {\n  font-size: $h3-size-mobile !important;\n  font-weight: $font-weight-light !important;\n\n  @include breakpoint(md) { font-size: $h3-size !important; }\n}\n\n// Smallest text size\n/* Set the font size to ${#h6-size} */\n.text-small { font-size: $h6-size !important; } // 12px\n\n/* Large leading paragraphs */\n.lead {\n  margin-bottom: 30px;\n  font-size: $h3-size;\n  font-weight: $font-weight-light;\n  color: $gray-600;\n}\n\n// Line-height variations\n// Close to commonly used line-heights. Most line-heights\n// combined with type size equate to whole pixels.\n// Will be improved with future typography scale updates.\n/* Set the line height to ultra condensed */\n.lh-condensed-ultra { line-height: $lh-condensed-ultra !important; }\n/* Set the line height to condensed */\n.lh-condensed { line-height: $lh-condensed !important; }\n/* Set the line height to default */\n.lh-default { line-height: $lh-default !important; }\n/* Set the line height to zero */\n.lh-0 { line-height: 0 !important; }\n\n// Text alignments\n// Responsive text alignment\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    /* Text align to the right */\n    .text#{$variant}-right { text-align: right !important; }\n    /* Text align to the left */\n    .text#{$variant}-left { text-align: left !important; }\n    /* Text align to the center */\n    .text#{$variant}-center { text-align: center !important; }\n  }\n}\n\n// Text styles\n/* Set the font weight to normal */\n.text-normal { font-weight: $font-weight-normal !important; }\n/* Set the font weight to bold */\n.text-bold { font-weight: $font-weight-bold !important;}\n/* Set the font to italic */\n.text-italic { font-style: italic !important; }\n/* Make text uppercase */\n.text-uppercase { text-transform: uppercase !important; }\n/* Underline text */\n.text-underline { text-decoration: underline !important; }\n/* Don't underline text */\n.no-underline { text-decoration: none !important; }\n/* Don't wrap white space */\n.no-wrap { white-space: nowrap !important; }\n/* Normal white space */\n.ws-normal { white-space: normal !important; }\n/* Allow long lines with no spaces to line break */\n.wb-break-all { word-break: break-all !important; }\n\n.text-emphasized {\n  font-weight: $font-weight-bold;\n  color: $text-gray-dark;\n}\n\n// List styles\n.list-style-none { list-style: none !important; }\n\n// Text Shadows\n/* Add a dark text shadow */\n.text-shadow-dark {\n  text-shadow: 0 1px 1px rgba($black, 0.25), 0 1px 25px rgba($black, 0.75);\n}\n/* Add a light text shadow */\n.text-shadow-light {\n  text-shadow: 0 1px 0 rgba($white, 0.5);\n}\n\n/* Set to monospace font */\n.text-mono {\n  font-family: $mono-font !important;\n}\n\n/* Disallow user from selecting text */\n.user-select-none {\n  user-select: none !important;\n}\n","// Visibility and display utilities\n// stylelint-disable primer/selector-no-utility\n// stylelint-disable block-opening-brace-space-after, block-opening-brace-space-before, comment-empty-line-before\n\n$display-values: (\n  block,\n  flex,\n  inline,\n  inline-block,\n  inline-flex,\n  none,\n  table,\n  table-cell\n);\n\n// Responsive display utilities\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    @each $display in $display-values {\n      .d#{$variant}-#{$display} { display: $display !important; }\n    }\n  }\n}\n\n.v-hidden { visibility: hidden !important; }\n.v-visible { visibility: visible !important; }\n\n// Hide utilities for each breakpoint\n// Each hide utility only applies to one breakpoint range.\n@media (max-width: $width-sm - 1px) {\n  .hide-sm {\n    display: none !important;\n  }\n}\n\n@media (min-width: $width-sm) and (max-width: $width-md - 1px) {\n  .hide-md {\n    display: none !important;\n  }\n}\n\n@media (min-width: $width-md) and (max-width: $width-lg - 1px) {\n  .hide-lg {\n    display: none !important;\n  }\n}\n\n@media (min-width: $width-lg) {\n  .hide-xl {\n    display: none !important;\n  }\n}\n\n/* Set the table-layout to fixed */\n.table-fixed { table-layout: fixed !important; }\n\n// Only display content to screen readers\n//\n// See: http://a11yproject.com/posts/how-to-hide-content/\n.sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  // Workaround for https://bugzilla.mozilla.org/show_bug.cgi?id=1241631\n  word-wrap: normal;\n  border: 0;\n}\n\n// Only display content on focus\n.show-on-focus {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  margin: 0;\n  overflow: hidden;\n  clip: rect(1px, 1px, 1px, 1px);\n\n  &:focus {\n    z-index: 20;\n    width: auto;\n    height: auto;\n    clip: auto;\n  }\n}\n","/*!\n * Primer-product\n * http://primer.github.io\n *\n * Released under MIT license. Copyright (c) 2019 GitHub Inc.\n */\n\n// Primer master file\n//\n// Imports all Primer files in their intended order for easy mass-inclusion.\n// Should you need specific files, you can easily use separate `@import`s.\n\n// Global requirements\n@import \"../support/index.scss\";\n\n// Product specific css modules\n@import \"../alerts/index.scss\";\n@import \"../avatars/index.scss\";\n@import \"../blankslate/index.scss\";\n@import \"../branch-name/index.scss\";\n@import \"../labels/index.scss\";\n@import \"../markdown/index.scss\";\n@import \"../popover/index.scss\";\n@import \"../progress/index.scss\";\n@import \"../subhead/index.scss\";\n","// Default flash\n// stylelint-disable selector-max-type\n.flash {\n  position: relative;\n  padding: $spacer-3;\n  color: $blue-800;\n  background-color: $blue-100;\n  border: 1px solid $border-black-fade;\n  border-radius: 3px;\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n}\n\n// Contain the flash messages\n.flash-messages {\n  margin-bottom: $spacer-4;\n}\n\n// Close button\n.flash-close {\n  float: right;\n  padding: $spacer-3;\n  margin: -$spacer-3;\n  color: inherit;\n  text-align: center;\n  cursor: pointer;\n  // Undo `<button>` styles\n  background: none;\n  border: 0;\n  appearance: none;\n  opacity: 0.6;\n\n  &:hover {\n    opacity: 1;\n  }\n}\n\n// Action button\n.flash-action {\n  float: right;\n  margin-top: -3px;\n  margin-left: $spacer-4;\n}\n\n//\n// Variations\n//\n\n.flash-warn {\n  color: $yellow-900;\n  background-color: $yellow-100;\n  border-color: $black-fade-15;\n}\n\n.flash-error {\n  color: $red-900;\n  background-color: $red-100;\n  border-color: $black-fade-15;\n}\n\n.flash-success {\n  color: $green-800;\n  background-color: $green-100;\n  border-color: $black-fade-15;\n}\n\n.flash-full {\n  margin-top: -1px;\n  border-width: 1px 0;\n  border-radius: 0;\n}\n\n// FIXME deprecate this\n.warning {\n  padding: $em-spacer-5;\n  margin-bottom: 0.8em;\n  font-weight: $font-weight-bold;\n  background-color: $yellow-100;\n}\n",".avatar {\n  display: inline-block;\n  overflow: hidden; // Ensure page layout in Firefox should images fail to load\n  line-height: $lh-condensed-ultra;\n  vertical-align: middle;\n  border-radius: 3px;\n}\n\n.avatar-small { border-radius: 2px; }\n\n.avatar-link {\n  float: left;\n  line-height: $lh-condensed-ultra;\n}\n\n// User for example on /stars and /user for grids of avatars\n.avatar-group-item {\n  display: inline-block;\n  margin-bottom: 3px;\n}\n","// .avatar-parent-child is when you see a small avatar at the bottom right\n// corner of a larger avatar.\n//\n// No Styleguide version\n.avatar-parent-child {\n  position: relative;\n}\n\n.avatar-child {\n  position: absolute;\n  right: -15%;\n  bottom: -9%;\n  background-color: $bg-white; // For transparent backgrounds\n  border-radius: 2px;\n  box-shadow: -2px -2px 0 rgba($white, 0.8);\n}\n","// Stacked avatars can be used to show who is participating in thread when\n// there is limited space available.\n\n.AvatarStack {\n  position: relative;\n  min-width: 26px;\n  height: 20px;\n\n  .AvatarStack-body {\n    position: absolute;\n  }\n\n  &.AvatarStack--two {\n    min-width: 36px;\n  }\n\n  &.AvatarStack--three-plus {\n    min-width: 46px;\n  }\n}\n\n.AvatarStack-body {\n  display: flex;\n  background: $bg-white;\n\n  .avatar {\n    position: relative;\n    z-index: 2;\n    display: flex;\n    width: 20px;\n    height: 20px;\n    box-sizing: content-box;\n    margin-right: -11px;\n    background-color: $bg-white;\n    border-right: $border-width $border-style $white;\n    border-radius: 2px;\n    transition: margin 0.1s ease-in-out;\n\n    &:first-child {\n      z-index: 3;\n    }\n\n    &:last-child {\n      z-index: 1;\n      border-right: 0;\n    }\n\n    // stylelint-disable selector-max-type\n    img {\n      border-radius: 2px;\n    }\n    // stylelint-enable selector-max-type\n\n    // Account for 4+ avatars\n    &:nth-child(n+4) {\n      display: none;\n      opacity: 0;\n    }\n  }\n\n  &:hover {\n    .avatar {\n      margin-right: 3px;\n    }\n\n    .avatar:nth-child(n+4) {\n      display: flex;\n      opacity: 1;\n    }\n\n    .avatar-more {\n      display: none !important;\n    }\n  }\n}\n\n.avatar.avatar-more {\n  z-index: 1;\n  margin-right: 0;\n  background: $gray-100;\n\n  &::before,\n  &::after {\n    position: absolute;\n    display: block;\n    height: 20px;\n    content: \"\";\n    border-radius: 2px;\n    outline: $border-width $border-style $white;\n  }\n\n  &::before {\n    width: 17px;\n    background: $gray-200;\n  }\n\n  &::after {\n    width: 14px;\n    background: $gray-300;\n  }\n}\n\n// Right aligned variation\n\n.AvatarStack--right {\n  .AvatarStack-body {\n    right: 0;\n    flex-direction: row-reverse;\n\n    &:hover .avatar {\n      margin-right: 0;\n      margin-left: 3px;\n    }\n  }\n\n  .avatar.avatar-more {\n    background: $gray-300;\n\n    &::before {\n      width: 5px;\n    }\n\n    &::after {\n      width: 2px;\n      background: $gray-100;\n    }\n  }\n\n  .avatar {\n    margin-right: 0;\n    margin-left: -11px;\n    border-right: 0;\n    border-left: $border-width $border-style $white;\n  }\n}\n","// Circle badge icon with drop shadow for icons and logos\n\n.CircleBadge {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: $white;\n  border-radius: 50%;\n  box-shadow: $box-shadow-medium;\n}\n\n.CircleBadge-icon {\n  max-width: 60% !important;\n  height: auto !important;\n  max-height: 55% !important;\n}\n\n// Small badge\n.CircleBadge--small {\n  width: 56px;\n  height: 56px;\n}\n\n// Medium badge\n.CircleBadge--medium {\n  width: 96px;\n  height: 96px;\n}\n\n// Large badge\n.CircleBadge--large {\n  width: 128px;\n  height: 128px;\n}\n\n// Dashed line that connects badges..\n// Wrap around 2 or more badges to create a horizonal line:\n\n.DashedConnection {\n  position: relative;\n\n  &::before {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    width: 100%;\n    content: \"\";\n    border-bottom: 2px dashed $border-gray;\n  }\n\n  .CircleBadge {\n    position: relative;\n  }\n}\n","// stylelint-disable selector-max-type\n.blankslate {\n  position: relative;\n  padding: $spacer-5;\n  text-align: center;\n  background-color: $gray-000;\n  border: 1px solid $gray-200;\n  border-radius: 3px;\n  box-shadow: inset 0 0 10px rgba($black, 0.05);\n\n  code {\n    padding: 2px 5px 3px;\n    font-size: $h5-size;\n    background: $bg-white;\n    border: 1px solid $border-gray-light;\n    border-radius: 3px;\n  }\n}\n\n.blankslate-icon {\n  margin-right: $spacer-1;\n  margin-bottom: $spacer-2;\n  margin-left: $spacer-1;\n  color: lighten($gray-400, 5%);\n}\n\n.blankslate-capped {\n  border-radius: 0 0 3px 3px;\n}\n\n.blankslate-spacious {\n  padding: ($spacer-6 * 2) $spacer-6;\n}\n\n.blankslate-narrow {\n  max-width: 485px;\n  margin: 0 auto;\n}\n\n// was .large-format\n.blankslate-large {\n  h3 {\n    margin: $spacer-3 0;\n    font-size: $h3-size;\n  }\n\n  p {\n    font-size: $h4-size;\n  }\n}\n\n// was .clean-background\n.blankslate-clean-background {\n  background: none;\n  border: 0;\n  box-shadow: none;\n}\n","// A nice way to display branch names inside the UI. Can be a link or not.\n// stylelint-disable selector-max-type\n.branch-name {\n  display: inline-block;\n  padding: 2px 6px;\n  font: 12px $mono-font;\n  color: rgba($black, 0.6);\n  background-color: lighten($blue-100, 3%);\n  border-radius: 3px;\n\n  .octicon {\n    margin: 1px -2px 0 0;\n    color: desaturate($blue-300, 70%);\n  }\n}\n\n// When a branch name is a link\n// stylelint-disable selector-no-qualifying-type\na.branch-name { color: $text-blue; }\n// stylelint-enable selector-no-qualifying-type\n","// Needs refactoring\n// stylelint-disable selector-no-qualifying-type\n// stylelint-disable selector-max-type\n// Labels\n//\n// Use labels to add keyword tags or phrases to issues and pull requests. These\n// are used within a list of issues/PRs, on individual issues/PRs, and within\n// the select menus that manage them.\n\n// Provide a wrapper to ensure labels stick together\n.labels {\n  position: relative;\n}\n\n// Each label gets common styles\n// .label will be deprecated for .Label\n.label,\n.Label {\n  display: inline-block;\n  padding: 3px $spacer-1;\n  font-size: $font-size-small;\n  font-weight: $font-weight-bold;\n  line-height: $lh-condensed-ultra; // prevent inheritance from changing proportions\n  color: $text-white;\n  border-radius: 2px;\n  box-shadow: inset 0 -1px 0 rgba($black, 0.12);\n\n  &:hover {\n    text-decoration: none;\n  }\n}\n\n// Custom GitHub labels (not for issues/PRs/etc)\n//\n// Commonly used across the site to indicate a particular role. For example,\n// \"repo owner\" within code comments or role in organizations.\n.Label--gray {\n  color: $gray-600;\n  background-color: lighten($gray-200, 3%);\n}\n\n.Label--outline {\n  margin-top: -1px; // offsets the 1px border\n  margin-bottom: -1px; // offsets the 1px border\n  font-weight: $font-weight-normal;\n  color: $text-gray;\n  background-color: transparent;\n  border: 1px solid $black-fade-15;\n  box-shadow: none;\n}\n\n.Label--outline-green {\n  color: $text-green;\n  border: 1px solid $border-green;\n}\n\n.Label--gray-darker {\n  background-color: $gray;\n}\n\n.Label--orange {\n  background-color: $bg-orange;\n}\n","// A rounded corner box containing a label \"open\" or \"closed\"\n// The default state is gray.\n//\n// open     - $green background\n// reopened - $green background\n// closed   - $red background\n// merged   - $purple background\n\n// .state to be deprecated in replace of .State\n.state,\n.State {\n  display: inline-block;\n  padding: $spacer-1 $spacer-2;\n  font-weight: $font-weight-bold;\n  line-height: 20px;\n  color: $text-white;\n  text-align: center;\n  background-color: $gray-500;\n  border-radius: 3px;\n}\n\n.State--green { background-color: darken($green-400, 5%); }\n\n.State--purple { background-color: $bg-purple; }\n\n.State--red { background-color: $red-600; }\n\n.State--small {\n  padding: $em-spacer-2 $spacer-1;\n  font-size: $h6-size;\n\n  .octicon {\n    width: 1em; // Ensures different icons don't change State indicator width\n  }\n}\n","// Counters are rounded-corner badges for numbers\n.Counter {\n  display: inline-block;\n  padding: 2px 5px;\n  font-size: $font-size-small;\n  font-weight: $font-weight-bold;\n  line-height: $lh-condensed-ultra;\n  color: $gray-600;\n  background-color: rgba($black, 0.08);\n  border-radius: 20px;\n\n  &:empty {\n    visibility: hidden;\n  }\n}\n\n.Counter--gray-light {\n  color: $text-gray-dark;\n  background-color: $black-fade-15;\n}\n\n.Counter--gray {\n  color: $white;\n  background-color: $gray;\n}\n","// All of our block level items should have the same margin\n// stylelint-disable selector-max-type\n\n// This is styling for generic markdownized text. Anything you put in a\n// container with .markdown-body on it should render generally well. It also\n// includes some GitHub Flavored Markdown specific styling (like @mentions)\n.markdown-body {\n  font-family: $body-font;\n  font-size: $h4-size;\n  line-height: $body-line-height;\n  word-wrap: break-word;\n\n  // Clearfix on the markdown body\n  &::before {\n    display: table;\n    content: \"\";\n  }\n\n  &::after {\n    display: table;\n    clear: both;\n    content: \"\";\n  }\n\n  > *:first-child {\n    margin-top: 0 !important;\n  }\n\n  > *:last-child {\n    margin-bottom: 0 !important;\n  }\n\n  // Anchors like <a name=\"examples\">. These sometimes end up wrapped around\n  // text when users mistakenly forget to close the tag or use self-closing tag\n  // syntax. We don't want them to appear like links.\n  // FIXME: a:not(:link):not(:visited) would be a little clearer here (and\n  // possibly faster to match), but it breaks styling of <a href> elements due\n  // to https://bugs.webkit.org/show_bug.cgi?id=142737.\n  a:not([href]) {\n    color: inherit;\n    text-decoration: none;\n  }\n\n  // Link Colors\n  .absent {\n    color: $red-600;\n  }\n\n  .anchor {\n    float: left;\n    padding-right: $spacer-1;\n    margin-left: -20px;\n    line-height: $lh-condensed-ultra;\n\n    &:focus {\n      outline: none;\n    }\n  }\n\n  p,\n  blockquote,\n  ul,\n  ol,\n  dl,\n  table,\n  pre,\n  details {\n    margin-top: 0;\n    margin-bottom: $spacer-3;\n  }\n\n  hr {\n    height: $em-spacer-3;\n    padding: 0;\n    margin: $spacer-4 0;\n    background-color: $gray-200;\n    border: 0;\n  }\n\n  blockquote {\n    padding: 0 1em;\n    color: $gray-500;\n    border-left: 0.25em solid lighten($gray-300, 5%);\n\n    > :first-child {\n      margin-top: 0;\n    }\n\n    > :last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  kbd {\n    display: inline-block;\n    padding: 3px 5px;\n    font-size: 11px;\n    line-height: 10px;\n    color: $gray-700;\n    vertical-align: middle;\n    background-color: $gray-000;\n    border: solid 1px darken($gray-300, 4%);\n    border-bottom-color: $gray-400;\n    border-radius: 3px;\n    box-shadow: inset 0 -1px 0 $gray-400;\n  }\n}\n","// Needs refactoring\n// stylelint-disable selector-max-compound-selectors, selector-max-specificity\n// stylelint-disable selector-max-type\n.markdown-body {\n  // Headings\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    margin-top: $spacer-4;\n    margin-bottom: $spacer-3;\n    font-weight: $font-weight-bold;\n    line-height: $lh-condensed;\n\n    .octicon-link {\n      color: $black;\n      vertical-align: middle;\n      visibility: hidden;\n    }\n\n    &:hover .anchor {\n      text-decoration: none;\n\n      .octicon-link {\n        visibility: visible;\n      }\n    }\n\n    tt,\n    code {\n      font-size: inherit;\n    }\n  }\n\n  h1 {\n    padding-bottom: 0.3em;\n    font-size: 2em;\n    border-bottom: 1px solid lighten($gray-200, 3%);\n  }\n\n  h2 {\n    padding-bottom: 0.3em;\n    font-size: 1.5em;\n    border-bottom: 1px solid lighten($gray-200, 3%);\n  }\n\n  h3 {\n    font-size: 1.25em;\n  }\n\n  h4 {\n    font-size: 1em;\n  }\n\n  h5 {\n    font-size: 0.875em;\n  }\n\n  h6 {\n    font-size: 0.85em;\n    color: $gray-500;\n  }\n}\n","// Base styles\n// stylelint-disable selector-no-qualifying-type\n// stylelint-disable selector-max-type\n.markdown-body {\n\n  // Lists, Blockquotes & Such\n  ul,\n  ol {\n    padding-left: 2em;\n\n    &.no-list {\n      padding: 0;\n      list-style-type: none;\n    }\n  }\n\n  // Did someone complain about list spacing? Encourage them\n  // to create the spacing with their markdown formatting.\n  // List behavior should be controled by the markup, not the css.\n  //\n  // For lists with padding between items, use blank\n  // lines between items. This will generate paragraphs with\n  // padding to space things out.\n  //\n  // - item\n  //\n  // - item\n  //\n  // - item\n  //\n  // For list without padding, don't use blank lines.\n  //\n  // - item\n  // - item\n  // - item\n  //\n  // Modifying the css to emulate these behaviors merely brakes\n  // one case in the process of solving another. Don't change\n  // this unless it's really really a bug.\n  ul ul,\n  ul ol,\n  ol ol,\n  ol ul {\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n\n  li {\n    word-wrap: break-all;\n  }\n\n  li > p {\n    margin-top: $spacer-3;\n  }\n\n  li + li {\n    margin-top: $em-spacer-3;\n  }\n\n  dl {\n    padding: 0;\n\n    dt {\n      padding: 0;\n      margin-top: $spacer-3;\n      font-size: 1em;\n      font-style: italic;\n      font-weight: $font-weight-bold;\n    }\n\n    dd {\n      padding: 0 $spacer-3;\n      margin-bottom: $spacer-3;\n    }\n  }\n}\n","// Needs refactoring\n// stylelint-disable selector-max-type\n.markdown-body {\n  // Tables\n  table {\n    display: block;\n    width: 100%;\n    overflow: auto;\n\n    th {\n      font-weight: $font-weight-bold;\n    }\n\n    th,\n    td {\n      padding: 6px 13px;\n      border: 1px solid lighten($gray-300, 5%);\n    }\n\n    tr {\n      background-color: $bg-white;\n      border-top: 1px solid darken($gray-300, 4%);\n\n      &:nth-child(2n) {\n        background-color: $gray-100;\n      }\n    }\n\n    img {\n      background-color: transparent;\n    }\n  }\n}\n","// Need to target base styles\n// stylelint-disable selector-max-compound-selectors, selector-no-qualifying-type, primer/selector-no-utility\n// stylelint-disable selector-max-type\n.markdown-body {\n  // Images & Stuff\n  img {\n    max-width: 100%;\n    // because we put padding on the images to hide header lines, and some people\n    // specify the width of their images in their markdown.\n    box-sizing: content-box;\n    background-color: $bg-white;\n\n    &[align=right] {\n      padding-left: 20px;\n    }\n\n    &[align=left] {\n      padding-right: 20px;\n    }\n  }\n\n  .emoji {\n    max-width: none;\n    vertical-align: text-top;\n    // Override `<img>` styles so Emjois don't clash with zebra striping in our tables\n    background-color: transparent;\n  }\n\n  // Gollum Image Tags\n\n  // Framed\n  span.frame {\n    display: block;\n    overflow: hidden;\n\n    > span {\n      display: block;\n      float: left;\n      width: auto;\n      padding: 7px;\n      margin: 13px 0 0;\n      overflow: hidden;\n      border: 1px solid lighten($gray-300, 5%);\n    }\n\n    span img {\n      display: block;\n      float: left;\n    }\n\n    span span {\n      display: block;\n      padding: 5px 0 0;\n      clear: both;\n      color: $text-gray-dark;\n    }\n  }\n\n  span.align-center {\n    display: block;\n    overflow: hidden;\n    clear: both;\n\n    > span {\n      display: block;\n      margin: 13px auto 0;\n      overflow: hidden;\n      text-align: center;\n    }\n\n    span img {\n      margin: 0 auto;\n      text-align: center;\n    }\n  }\n\n  span.align-right {\n    display: block;\n    overflow: hidden;\n    clear: both;\n\n    > span {\n      display: block;\n      margin: 13px 0 0;\n      overflow: hidden;\n      text-align: right;\n    }\n\n    span img {\n      margin: 0;\n      text-align: right;\n    }\n  }\n\n  span.float-left {\n    display: block;\n    float: left;\n    margin-right: 13px;\n    overflow: hidden;\n\n    span {\n      margin: 13px 0 0;\n    }\n  }\n\n  span.float-right {\n    display: block;\n    float: right;\n    margin-left: 13px;\n    overflow: hidden;\n\n    > span {\n      display: block;\n      margin: 13px auto 0;\n      overflow: hidden;\n      text-align: right;\n    }\n  }\n}\n","// stylelint-disable selector-max-type\n.markdown-body {\n  // Inline code snippets\n  code,\n  tt {\n    padding: 0.2em 0.4em;\n    margin: 0;\n    font-size: 85%;\n    background-color: rgba($black, 0.05);\n    border-radius: $border-radius;\n\n    br { display: none; }\n  }\n\n  del code { text-decoration: inherit; }\n\n  pre {\n    word-wrap: normal;\n\n    // Code tags within code blocks (<pre>s)\n    > code {\n      padding: 0;\n      margin: 0;\n      font-size: 100%;\n      word-break: normal;\n      white-space: pre;\n      background: transparent;\n      border: 0;\n    }\n  }\n\n  .highlight {\n    margin-bottom: $spacer-3;\n\n    pre {\n      margin-bottom: 0;\n      word-break: normal;\n    }\n  }\n\n  .highlight pre,\n  pre {\n    padding: $spacer-3;\n    overflow: auto;\n    font-size: 85%;\n    line-height: 1.45;\n    background-color: $gray-100;\n    border-radius: $border-radius;\n  }\n\n  pre code,\n  pre tt {\n    display: inline;\n    max-width: auto;\n    padding: 0;\n    margin: 0;\n    overflow: visible;\n    line-height: inherit;\n    word-wrap: normal;\n    background-color: transparent;\n    border: 0;\n  }\n}\n","// stylelint-disable selector-max-type\n.markdown-body .csv-data {\n  td,\n  th {\n    padding: 5px;\n    overflow: hidden;\n    font-size: $font-size-small;\n    line-height: $lh-condensed-ultra;\n    text-align: left;\n    white-space: nowrap;\n  }\n\n  .blob-num {\n    padding: 10px $spacer-2 9px;\n    text-align: right;\n    background: $bg-white;\n    border: 0;\n  }\n\n  tr { border-top: 0; }\n\n  th {\n    font-weight: $font-weight-bold;\n    background: $gray-100;\n    border-top: 0;\n  }\n}\n",".Popover {\n  position: absolute;\n  z-index: 100;\n}\n\n.Popover-message {\n  position: relative;\n  width: 232px;\n  margin-right: auto;\n  margin-left: auto;\n\n  // Carets\n  &::before,\n  &::after {\n    position: absolute;\n    left: 50%;\n    display: inline-block;\n    content: \"\";\n  }\n\n  &::before {\n    top: -$spacer-3;\n    margin-left: -9px;\n    border: $spacer-2 $border-style transparent;\n    border-bottom-color: $border-black-fade;\n  }\n\n  &::after {\n    top: -14px;\n    margin-left: -$spacer-2;\n    border: 7px $border-style transparent;\n    border-bottom-color: $bg-white;\n  }\n}\n\n// Bottom-oriented carets\n.Popover-message--bottom,\n.Popover-message--bottom-right,\n.Popover-message--bottom-left {\n  &::before,\n  &::after {\n    top: auto;\n    border-bottom-color: transparent;\n  }\n\n  &::before {\n    bottom: -$spacer-3;\n    border-top-color: $border-black-fade;\n  }\n\n  &::after {\n    bottom: -14px;\n    border-top-color: $bg-white;\n  }\n}\n\n// Top & Bottom: Right-oriented carets\n.Popover-message--top-right,\n.Popover-message--bottom-right {\n  right: -9px;\n  margin-right: 0;\n\n  &::before,\n  &::after {\n    left: auto;\n    margin-left: 0;\n  }\n\n  &::before {\n    right: 20px;\n  }\n\n  &::after {\n    right: 21px;\n  }\n}\n\n// Top & Bottom: Left-oriented carets\n.Popover-message--top-left,\n.Popover-message--bottom-left {\n  left: -9px;\n  margin-left: 0;\n\n  &::before,\n  &::after {\n    left: $spacer-4;\n    margin-left: 0;\n  }\n\n  &::after {\n    left: $spacer-4 + 1;\n  }\n}\n\n// Right- & Left-oriented carets\n.Popover-message--right,\n.Popover-message--right-top,\n.Popover-message--right-bottom,\n.Popover-message--left,\n.Popover-message--left-top,\n.Popover-message--left-bottom {\n  &::before,\n  &::after {\n    top: 50%;\n    left: auto;\n    margin-left: 0;\n    border-bottom-color: transparent;\n  }\n\n  &::before {\n    margin-top: -($spacer-2 + 1);\n  }\n\n  &::after {\n    margin-top: -$spacer-2;\n  }\n}\n\n// Right-oriented carets\n.Popover-message--right,\n.Popover-message--right-top,\n.Popover-message--right-bottom {\n  &::before {\n    right: -$spacer-3;\n    border-left-color: $border-black-fade;\n  }\n\n  &::after {\n    right: -14px;\n    border-left-color: $bg-white;\n  }\n}\n\n// Left-oriented carets\n.Popover-message--left,\n.Popover-message--left-top,\n.Popover-message--left-bottom {\n  &::before {\n    left: -$spacer-3;\n    border-right-color: $border-black-fade;\n  }\n\n  &::after {\n    left: -14px;\n    border-right-color: $bg-white;\n  }\n}\n\n// Right & Left: Top-oriented carets\n.Popover-message--right-top,\n.Popover-message--left-top {\n  &::before,\n  &::after {\n    top: $spacer-4;\n  }\n}\n\n// Right & Left: Bottom-oriented carets\n.Popover-message--right-bottom,\n.Popover-message--left-bottom {\n  &::before,\n  &::after {\n    top: auto;\n  }\n\n  &::before {\n    bottom: $spacer-3;\n  }\n\n  &::after {\n    bottom: $spacer-3 + 1;\n  }\n}\n\n.Popover-message--large {\n\n  @include breakpoint(sm) {\n    min-width: 320px;\n  }\n}\n","// Progress\n\n.Progress {\n  display: flex;\n  height: 8px;\n  overflow: hidden;\n  background-color: $gray-200;\n  border-radius: $border-radius;\n}\n\n.Progress--large {\n  height: 10px;\n}\n\n.Progress--small {\n  height: 5px;\n}\n","// Subhead styles\n.Subhead {\n  display: flex;\n  padding-bottom: $spacer-2;\n  margin-bottom: $spacer-3;\n  border-bottom: $border;\n  flex-flow: row wrap;\n}\n\n// Modifier class to give a lot of breathing room between sections of content.\n.Subhead--spacious {\n  margin-top: $spacer-6;\n}\n\n// <h2> sized heading with normal font weight\n.Subhead-heading {\n  font-size: $h2-size;\n  font-weight: normal;\n  flex: 1 1 auto;\n}\n\n// Make the text bold and red for dangerous content\n.Subhead-heading--danger {\n  font-weight: $font-weight-bold;\n  color: $text-red;\n}\n\n// One-liner of supporting text\n.Subhead-description {\n  font-size: $body-font-size;\n  color: $text-gray;\n  flex: 1 100%;\n}\n\n// Add 1 or 2 buttons to the right of the heading\n.Subhead-actions {\n  align-self: center;\n  justify-content: flex-end;\n}\n","/*!\n * Primer-marketing\n * http://primer.github.io\n *\n * Released under MIT license. Copyright (c) 2019 GitHub Inc.\n */\n\n// Primer master file\n//\n// Imports all Primer files in their intended order for easy mass-inclusion.\n// Should you need specific files, you can easily use separate `@import`s.\n\n// Global requirements\n@import \"./support/index.scss\";\n\n// marketing specific css modules\n@import \"./type/index.scss\";\n@import \"./buttons/index.scss\";\n@import \"./utilities/index.scss\";\n","$marketing-font-path: \"/primer-marketing-support/fonts/\" !default;\n\n// Type\n@font-face {\n  font-family: Inter;\n  font-style: normal;\n  font-weight: $font-weight-normal;\n  src: local(\"Inter\"), local(\"Inter-Regular\"), url(\"#{$marketing-font-path}Inter-Regular.woff\") format(\"woff\");\n}\n\n@font-face {\n  font-family: Inter;\n  font-style: normal;\n  font-weight: $font-weight-semibold;\n  src: local(\"Inter Medium\"), local(\"Inter-Medium\"), url(\"#{$marketing-font-path}Inter-Medium.woff\") format(\"woff\");\n}\n\n$font-mktg: Inter, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\" !default;\n\n// Builds upon primer-support/variables/typography.scss\n$h000-size: 64px !default;\n$h000-size-mobile: 48px !default;\n\n// Colors\n$green-mktg: mix($green-500, $green-400, 50%);\n$blue-mktg: mix($blue-400, $blue-500, 42%);\n\n// Animations\n$transition-time: 0.4s;\n\n// Increases primer-core scale first by 8px for spacer-7 then by 16px step increments for spacer-8 to spacer-12\n// i.e. After 40px, we have 48, 64, 80, 96, etc.\n$spacer-7:  $spacer * 6 !default;  // 48px\n$spacer-8:  $spacer * 8 !default;  // 64px\n$spacer-9:  $spacer * 10 !default; // 80px\n$spacer-10: $spacer * 12 !default; // 96px\n$spacer-11: $spacer * 14 !default; // 112px\n$spacer-12: $spacer * 16 !default; // 128px\n\n$marketingSpacers: $spacer-7, $spacer-8, $spacer-9, $spacer-10, $spacer-11, $spacer-12;\n$allSpacers: $spacer-1, $spacer-2, $spacer-3, $spacer-4, $spacer-5, $spacer-6, $spacer-7, $spacer-8, $spacer-9, $spacer-10, $spacer-11, $spacer-12;\n\n$marketing-spacers: (\n  7: $spacer-7,\n  8: $spacer-8,\n  9: $spacer-9,\n  10: $spacer-10,\n  11: $spacer-11,\n  12: $spacer-12,\n) !default;\n\n$marketing-all-spacers: map-merge(\n  (\n    0: 0,\n    1: $spacer-1,\n    2: $spacer-2,\n    3: $spacer-3,\n    4: $spacer-4,\n    5: $spacer-5,\n    6: $spacer-6,\n  ),\n  $marketing-spacers,\n) !default;\n\n$marketing-position-variants: (\n  \"\": \"\",\n  md: \"-md\",\n  lg: \"-lg\",\n) !default;\n","// Headings\n\n.h000-mktg,\n.h00-mktg,\n.h0-mktg,\n.h1-mktg,\n.h2-mktg,\n.h3-mktg,\n.h4-mktg,\n.h5-mktg,\n.h6-mktg,\n.lead-mktg {\n  font-family: $font-mktg;\n  font-weight: $font-weight-semibold;\n}\n\n@mixin h000-mktg {\n  font-size: $h000-size-mobile !important;\n  @include breakpoint(md) { font-size: $h000-size !important; }\n}\n\n.h000-mktg { @include h000-mktg; }\n\n@mixin h00-mktg {\n  font-size: $h00-size-mobile !important;\n  @include breakpoint(md) { font-size: $h00-size !important; }\n}\n\n.h00-mktg { @include h00-mktg; }\n\n@mixin h0-mktg {\n  font-size: $h0-size-mobile !important;\n  @include breakpoint(md) { font-size: $h0-size !important; }\n}\n\n.h0-mktg { @include h0-mktg; }\n\n@mixin h1-mktg {\n  font-size: $h1-size-mobile !important;\n  @include breakpoint(md) { font-size: $h1-size !important; }\n}\n\n.h1-mktg { @include h1-mktg; }\n\n@mixin h2-mktg {\n  font-size: $h2-size-mobile !important;\n  @include breakpoint(md) { font-size: $h2-size !important; }\n}\n\n.h2-mktg { @include h2-mktg; }\n\n@mixin h3-mktg {\n  font-size: $h3-size-mobile !important;\n  @include breakpoint(md) { font-size: $h3-size !important; }\n}\n\n.h3-mktg { @include h3-mktg; }\n\n.h4-mktg {\n  font-size: $h4-size !important;\n}\n\n.h5-mktg {\n  font-size: $h5-size !important;\n}\n\n.h6-mktg {\n  font-size: $h6-size !important;\n}\n\n// Big opening paragraphs\n@mixin lead-mktg {\n  font-size: $h3-size;\n  font-weight: $font-weight-normal;\n}\n\n.lead-mktg { @include lead-mktg; }\n\n// Pullquote\n\n@mixin pullquote {\n  padding-top: 0;\n  padding-bottom: 0;\n  padding-left: $spacer;\n  margin-bottom: $spacer-4;\n  font-family: $mono-font;\n  font-size: $h4-size;\n  line-height: 1.4;\n  color: $text-gray;\n  border-left: 3px solid $border-color;\n\n  @include breakpoint(md) {\n    padding-left: $spacer * 1.5;\n    margin-bottom: $spacer-5;\n    margin-left: (-$spacer * 1.5) - 3px;\n    font-size: 18px;\n    line-height: $lh-default;\n  }\n}\n\n.pullquote {\n  @include pullquote;\n}\n",".btn-mktg {\n  display: inline-block;\n  padding: $spacer-3 $spacer-4;\n  font-size: $h5-size;\n  font-weight: $font-weight-semibold;\n  color: $white;\n  white-space: nowrap;\n  vertical-align: middle;\n  cursor: pointer;\n  user-select: none;\n  background-color: $blue-mktg;\n  border: 1px solid $blue-mktg;\n  border-radius: $border-radius;\n  transition: $transition-time / 2;\n  appearance: none; // Corrects inability to style clickable `input` types in iOS.\n\n  &:hover {\n    text-decoration: none;\n    background-color: $blue-500;\n    border-color: $blue-500;\n  }\n\n  &:focus {\n    outline: 0;\n    box-shadow: 0 0 0 0.2em rgba($blue-500, 0.3);\n  }\n\n  &:disabled,\n  &.disabled {\n    pointer-events: none; // Disable hover styles\n    cursor: default;\n    opacity: 0.65;\n  }\n}\n\n.btn-primary-mktg {\n  background-color: $green-mktg;\n  border-color: $green-mktg;\n\n  &:hover {\n    background-color: $green-500;\n    border-color: $green-500;\n  }\n\n  &:focus {\n    box-shadow: 0 0 0 0.2em rgba($green-500, 0.3);\n  }\n}\n\n.btn-large-mktg {\n  padding: 20px $spacer-5;\n  font-size: $h4-size;\n}\n\n.btn-outline-mktg {\n  color: $blue-mktg;\n  background-color: rgba($white, 0);\n  border-color: rgba($blue-mktg, 0.5);\n\n  &:hover {\n    color: $blue-500;\n    text-decoration: none;\n    background-color: rgba($white, 0);\n    border-color: rgba($blue-mktg, 1);\n  }\n}\n\n@mixin btn-transparent-active {\n  color: $gray-800;\n  background-color: $white;\n  background-image: none;\n  border-color: $white;\n}\n\n.btn-transparent {\n  color: $white;\n  background-color: transparent;\n  background-image: none;\n  border: $border-width $border-style $white-fade-50;\n\n  &:hover,\n  &:active {\n    @include btn-transparent-active;\n  }\n}\n","// stylelint-disable no-unsupported-browser-features\n.grayscale {\n  filter: grayscale(100%);\n}\n","// Layout utilities\n// stylelint-disable block-opening-brace-space-before, primer/selector-no-utility, comment-empty-line-before\n\n// Responsive utilities to position content\n// No utilities for sm and xl breakpoints\n@each $breakpoint, $variant in $marketing-position-variants {\n  @include breakpoint($breakpoint) {\n    @each $scale, $size in $marketing-all-spacers {\n      .top#{$variant}-#{$scale}     { top: $size !important; }\n      .right#{$variant}-#{$scale}   { right: $size !important; }\n      .bottom#{$variant}-#{$scale}  { bottom: $size !important; }\n      .left#{$variant}-#{$scale}    { left: $size !important; }\n\n      .top#{$variant}-n#{$scale}     { top: -$size !important; }\n      .right#{$variant}-n#{$scale}   { right: -$size !important; }\n      .bottom#{$variant}-n#{$scale}  { bottom: -$size !important; }\n      .left#{$variant}-n#{$scale}    { left: -$size !important; }\n    }\n  }\n}\n\n// Negative offset columns\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    @for $offset from 1 through 7 {\n      .offset#{$variant}-n#{$offset} { margin-left: -($offset / 12 * 100%); }\n    }\n  }\n}\n","// Margin spacer utilities for marketing\n// Utilities only added for y-direction margin (i.e. top & bottom)\n// stylelint-disable block-opening-brace-space-before, declaration-colon-space-before, primer/selector-no-utility, comment-empty-line-before\n\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    @each $scale, $size in $marketing-spacers {\n\n      .mt#{$variant}-#{$scale} { margin-top: $size !important; }\n      .mb#{$variant}-#{$scale} { margin-bottom: $size !important; }\n\n      .my#{$variant}-#{$scale} {\n        margin-top: $size !important;\n        margin-bottom: $size !important;\n      }\n    }\n  }\n}\n","// Padding spacer utilities for marketing\n// stylelint-disable block-opening-brace-space-before, declaration-colon-space-before\n// stylelint-disable comment-empty-line-before\n\n@each $breakpoint, $variant in $responsive-variants {\n  @include breakpoint($breakpoint) {\n    @each $scale, $size in $marketing-spacers {\n\n      /* Set a #{$size} padding for all sides */\n      .p#{$variant}-#{$scale}  { padding: #{$size} !important; }\n\n      /* Set a #{$size} padding to the top */\n      .pt#{$variant}-#{$scale}  { padding-top: #{$size} !important; }\n      /* Set a #{$size} padding to the right */\n      .pr#{$variant}-#{$scale}  { padding-right: #{$size} !important; }\n      /* Set a #{$size} padding to the bottom */\n      .pb#{$variant}-#{$scale}  { padding-bottom: #{$size} !important; }\n      /* Set a #{$size} padding to the left */\n      .pl#{$variant}-#{$scale}  { padding-left: #{$size} !important; }\n\n      /* Set a #{$size} padding to the top & bottom */\n      .py#{$variant}-#{$scale} {\n        padding-top: #{$size} !important;\n        padding-bottom: #{$size} !important;\n      }\n    }\n  }\n}\n",".octicon {\n  display: inline-block;\n  vertical-align: text-top;\n  fill: currentColor;\n}\n","img {\n  max-width: 100%;\n  object-fit: contain;\n  object-position: top left; }\n"]}]);

